import { isStorageReadable, isStorageWritable } from 'alfa-common/lib/utils/storage';

export class Storage {
    constructor() {
        this.storage = localStorage;
        this.isReadable = isStorageReadable(this.storage);
        this.isWritable = isStorageWritable(this.storage);
    }

    read(key) {
        if (!this.isReadable) {
            return null;
        }

        return this.storage.getItem(key);
    }

    write(key, value) {
        if (!this.isWritable) {
            return;
        }

        const valueType = typeof value;
        if (valueType === 'object') {
            value = JSON.stringify(value);
        }

        this.storage.setItem(key, value);
    }

    remove(key) {
        if (!this.isWritable) {
            return;
        }

        this.storage.removeItem(key);
    }
}



