import EventSystem from '@/system/eventSystem';
import { getCursorPosition, normalizePercent } from './utils';
import { STYLES } from '@/ui/styles';

export const EVENTS = {
    VOLUME_CHANGE: 'volume_change'
};
const RAIL_MARGIN_TOP = 21;
const RAIL_MARGIN_BOTTOM = 48;
const THUMB_SIZE = 16;

export class VolumeBar extends EventSystem {
    constructor() {
        super();

        this.element = null;
        this.thumb = null;
        this.railCurrent = null;

        this.hideTimer = -1;
    }

    create(parent) {
        if (!parent) {
            console.log('[alfa] VolumeBar.create: no parent specified');
            return;
        }

        // root DOM element
        this.element = document.createElement('div');
        this.element.className = `${STYLES.VOLUME_BAR} ${STYLES.TRANSITION_VISIBLE} ${STYLES.OPACITY_HIDDEN}`;

        // default 'gray' volume rail group & rail
        const railGroup = document.createElement('span');
        railGroup.className = `${STYLES.VOLUME_BAR_RAIL_GROUP}`;
        const rail = this.railDefault = document.createElement('span');
        rail.className = `${STYLES.VOLUME_BAR_RAIL}`;
        railGroup.appendChild(rail);

        // active 'red' volume rail group & rail
        const railCurrentGroup = document.createElement('span');
        railCurrentGroup.className = `${STYLES.VOLUME_BAR_CURRENT_GROUP}`;
        this.railCurrent = document.createElement('span');
        this.railCurrent.className = `${STYLES.VOLUME_BAR_CURRENT}`;
        railCurrentGroup.appendChild(this.railCurrent);

        // volume thumb
        this.thumb = document.createElement('div');
        this.thumb.className = `${STYLES.VOLUME_BAR_THUMB}`;

        this.element.appendChild(railGroup);
        this.element.appendChild(railCurrentGroup);
        this.element.appendChild(this.thumb);

        this.element.addEventListener('click', (e) => this.onClick(e));

        parent.appendChild(this.element);
    }

    setVolume(volume) {
        const railHeight = this.railDefault.offsetHeight;

        const thumbBottom = volume * railHeight + RAIL_MARGIN_BOTTOM - (THUMB_SIZE / 2);
        this.thumb.style.bottom = thumbBottom + 'px';

        const railTop = (1 - volume) * railHeight + RAIL_MARGIN_BOTTOM - RAIL_MARGIN_TOP - (THUMB_SIZE / 2);
        this.railCurrent.style.top = railTop + 'px';
    }

    show() {
        clearTimeout(this.hideTimer);
        this.element.classList.remove(STYLES.OPACITY_HIDDEN);
    }

    hide() {
        this.__setupHideTimer();
    }

    onClick(e) {
        e.stopPropagation();

        const barHeight = this.element.offsetHeight;
        const railHeight = this.railDefault.offsetHeight;

        const pos = getCursorPosition(this.element, e);
        let percent = (barHeight - RAIL_MARGIN_BOTTOM - pos.y) / railHeight;
        percent = normalizePercent(percent);

        // notify UI about volume change
        this.emit(EVENTS.VOLUME_CHANGE, percent);
    }

    __setupHideTimer() {
        clearTimeout(this.hideTimer);
        this.hideTimer = setTimeout(() => {
            this.element.classList.add(STYLES.OPACITY_HIDDEN);
        }, 300);
    }
}



