import { assign } from 'alfa-common/lib/polyfills/assign';
import { flatten, pick } from 'alfa-common/lib/utils/array';
import { MIME, VAST_EVENTS } from '@/data/constants';

const VAST_TYPE = {
    VPAID: 'vpaid',
    WRAPPER: 'wrapper',
    INLINE: 'inline'
};

export class Vast {
    constructor(version) {
        this.version = version || 0;
        this.source = '';
        this.adTags = [];
    }

    static looksLikeVast(text) {
        text = (text || '').trim();
        const STARTS = [
            '<?xml',
            '<VAST',
            '<VideoAdServingTemplate'
        ];
        for (let i = 0; i < STARTS.length; i++) {
            if (text.indexOf(STARTS[i]) === 0) {
                return true;
            }
        }
        return false;
    }
}

export class AdTag {
    constructor() {
        // VAST data
        this.adSystem = '';
        this.error = '';
        this.impressions = [];
        this.linears = [];
        this.companions = [];
        this.nonLinears = [];
        this.icons = [];

        // selection from entire VAST
        this.linear = null;
        this.mediaFile = null;

        // events
        this.eventsLinear = {};  // merged from all linears
        this.eventsNonLinear = {};
    }

    getSupportedNonLinear() {
        for (let i = 0; i < this.nonLinears.length; i++) {
            if (this.nonLinears[i].isSupported()) {
                return this.nonLinears[i];
            }
        }
        return null;
    }

    hasIcons() {
        return this.icons && this.icons.length > 0;
    }

    getIconsForTime(seconds) {
        if (!this.hasIcons()) {
            return [];
        }

        // filter by time
        const byTime = this.icons.filter((icon) => icon.isValidForTime(seconds));

        // filter by 'program' - take one by each program, closest to actual video
        const byProgram = {};
        byTime.forEach((icon) => {
            byProgram[icon.program] = icon;
        });

        const result = [];
        for (let prop in byProgram) {
            result.push(byProgram[prop]);
        }

        return result;
    }

    hasProgressEvents() {
        const events = this.eventsLinear[VAST_EVENTS.PROGRESS];
        return events && events.length > 0;
    }

    mergeLinearEvents() {
        this.linears.forEach((linear) => {
            Object.keys(linear.events).forEach((event) => {
                if (!this.eventsLinear[event]) {
                    this.eventsLinear[event] = [];
                }
                [].push.apply(this.eventsLinear[event], linear.events[event]);
            });
        });
    }

    selectMediaFile(ua, screenSize) {
        // collect all mediafiles in one array
        const mediaFiles = flatten(pick(this.linears, 'mediaFiles'));

        // if we don't find any mediafiles - just exit
        if (mediaFiles.length === 0) {
            return;
        }

        // select appropriate mediafile by format, resolution and bitrate
        const mediaFile = MediaFile.pickVideo(mediaFiles, ua, screenSize);

        // select linear that has selected mediaFile
        const linears = this.linears.filter((item) => item.mediaFiles.indexOf(mediaFile) >= 0);
        if (linears && linears.length > 0) {
            this.linear = linears[0];
        }

        // copy selected mediafile data, so we can edit it
        this.mediaFile = assign(new MediaFile(), mediaFile);
        if (this.mediaFile.url && this.linear && this.linear.parameters) {
            this.mediaFile.url += '?' + this.linear.parameters;
        }
    }

    getVideoClickTrackings() {
        return this.linears ? flatten(pick(this.linears, 'clickTrackings')) : [];
    }

    getVastType() {
        if (this.mediaFile) {
            return this.mediaFile.isVPAID() ? VAST_TYPE.VPAID : VAST_TYPE.INLINE;
        }
        return VAST_TYPE.WRAPPER;
    }

    get videoClickThrough() {
        return this.linear ? this.linear.clickThrough : '';
    }

    get adParameters() {
        return this.linear ? this.linear.parameters : '';
    }

    get apiFramework() {
        return this.mediaFile ? this.mediaFile.apiFramework : '';
    }
}

export class Linear {
    constructor() {
        this.duration = 0;
        this.skipOffset = 0;
        this.parameters = null;
        this.events = {};
        this.mediaFiles = [];
        this.clickThrough = '';
        this.clickTrackings = [];
        this.clickCustoms = [];
    }
}

export class MediaFile {
    constructor() {
        this.delivery = '';
        this.type = '';
        this.width = 0;
        this.height = 0;
        this.codec = '';
        this.id = '';
        this.bitrate = 0;
        this.scalable = true;
        this.maintainAspectRatio = true;
        this.url = '';
        this.apiFramework = '';
    }

    isVPAID() {
        return this.type === MIME.JS || this.type === MIME.FLASH;
    }

    static pickVideo(mediaFiles, ua, { width, height }) {
        // filter out VPAID files
        let vpaidFiles = mediaFiles.filter((item) => item.isVPAID());

        // try to look for VPAID
        if (vpaidFiles.length > 0) {
            // ios only supports JS VPAID
            if (ua.isIOS) {
                vpaidFiles = vpaidFiles.filter((item) => item.type === MIME.JS);
            }

            // try to pick media with largest dimensions
            const largestMediaFile = vpaidFiles.reduce(pickLargest, null);
            if (largestMediaFile) {
                return largestMediaFile;
            }

            // try to pick media with smallest dimensions
            const smallestMediaFile = vpaidFiles.reduce(pickSmallest, null);
            if (smallestMediaFile) {
                return smallestMediaFile;
            }
        }

        // pick only media files with supported format
        const supportedMediaFiles = mediaFiles.filter((item) => ua.isSupportedVideoFormat(item.type));

        // look for largest media file of supported formats
        const largestMediaFile = supportedMediaFiles.reduce(pickLargest, null);
        if (largestMediaFile) {
            return largestMediaFile;
        }

        // if we couldn't find appropriate media file by size, then pick smallest available
        return supportedMediaFiles.reduce(pickSmallest, null);

        function pickLargest(largest, item) {
            // skip files that larger than screen or over bitrate limit
            if (item.width > width ||
                item.height > height ||
                item.bitrate > ua.bitrateLimit) {
                return largest;
            }

            if (!largest) {
                return item;
            }

            // pick largest media ...
            if (item.width > largest.width || item.height > largest.height) {
                return item;
            }

            // ... or media with the biggest bitrate
            if (item.width === largest.width && item.height === largest.height && item.bitrate > largest.bitrate) {
                return item;
            }

            return largest;
        }

        function pickSmallest(smallest, item) {
            if (!smallest) {
                return item;
            }

            // pick smallest by width media
            if (item.width < smallest.width) {
                return item;
            }

            return smallest;
        }
    }
}

export function Companion() {
    this.width = 0;
    this.height = 0;
    this.staticResource = null;
    this.htmlResource = null;
    this.iframeResource = null;
    this.clickThrough = null;
    this.events = {};
}

export class NonLinear {
    constructor() {
        this.type = null;
        this.width = 0;
        this.height = 0;
        this.staticResource = null;
        this.htmlResource = null;
        this.iframeResource = null;
        this.clickThrough = null;
        this.clickTracking = null;
    }

    isSupported() {
        return NonLinear.isSupported(this);
    }

    static isSupported(nonLinear) {
        if (!nonLinear.staticResource) {
            return false;
        }
        return nonLinear.type && ('' + nonLinear.type.indexOf('image')) >= 0;
    }
}

export class Icon {
    constructor() {
        this.index = 0;
        // required
        this.program = '';
        this.width = 0;
        this.height = 0;
        this.xPosition = 0;
        this.yPosition = 0;
        // resource
        this.staticResource = '';
        this.staticResourceType = '';
        this.htmlResource = '';
        this.iframeResource = '';
        // optional
        this.apiFramework = '';
        this.offset = 0;
        this.duration = 0;

        this.clickThrough = '';
        this.viewTracking = [];
        this.clickTracking = [];
    }

    isValidForTime(seconds) {
        return this.offset <= seconds && (this.offset + this.duration) >= seconds;
    }
}



