export function isUrl(str) {
    const URL_PREFIXES = [
        '//',
        'http://',
        'https://'
    ];

    // prepare input
    str = (str || '').trim().toLowerCase();
    if (!str) {
        return false;
    }

    // check
    for (let i = 0; i < URL_PREFIXES.length; i++) {
        const prefix = URL_PREFIXES[i];
        if (str.substr(0, prefix.length) === prefix) {
            return true;
        }
    }

    return false;
}

export function ensureProtocol(url, protocol) {
    url = (url || '').trim();
    if (!url) {
        return '';
    }
    if (!url.match(/^[a-zA-Z]+:\/\//i)) {
        return protocol + '//' + url;
    } else {
        return url;
    }
}



