import { assign } from 'alfa-common/lib/polyfills/assign';
import { ensureProtocol } from 'alfa-common/lib/utils/url';
import { get } from 'alfa-common/lib/utils/object';

import { URL } from '@/data/constants';
import { settings } from '@/data/settings';
import { parseYoutubeID } from '@/utils/url';

const VAST_TAG_DOMAIN = 'videmob.com';

/**
 * External config format:
 *
 * {
 *   "adult": boolean,     // should player show adult warning dialog before playing video
 *   "videoURL": string,   // Video file to play URL
 *   "poster": string,     // An image preview to the video before playing starts URL
 *   "title": string,      // A string that describes the video title. Title is used for the share mechanism and ads targeting
 *   "description": string,  // Textual video description. Description is used for share mechanism and ads targeting
 *   "mute": boolean,      // Specifies whether the video should start playing muted or not
 *   "autoPlay": boolean,  // Specifies whether the video should start playing automaticaly when page is loaded
 *   "parentId": string,   // HTML element id which will be the parent for the player
 *   "adonly": boolean,    // Set to true if you want to show an ad without videoURL parameter (for players created with the 'create' function),
 *   "height": number,     // Player height (optional  in pixels)
 *   "width": number,      // Player width (optional  in pixels)
 *   "site_url": string,   // Site URL where the player is being used
 *   "passback": string,      // 3rd party URL for returning an ad in case an ad is not returned for main request. (optional)
 *   "passback_js": string,   // JavaScript code or URL to JavaScript code to return ad in case of an ad is not returned for main request. (optional)
 *   "passback_html": string, // URL to HTML page to return ad in case of an ad is not returned for main request. (optional)
 *
 *   "app_bundle": string, // is player injected in native application
 *   "ads": {
 *     "vasturl": string,
 *     "vastxml": string,
 *     "autogetad": boolean,  // default = true
 *     "skipoffset": number
 *   },
 *   "isVPAID": boolean,
 *   "videoTag": string,
 *   "gui": {
 *     "noGUI": boolean,         // disable bottom control panel
 *     "noFullscreen": boolean,  // disable fullscreen functionality
 *     "noSharing": boolean,     // disable sharing controls
 *     "muteSwitch": boolean     // show\hide floating mute switch
 *   },
 *   "androidsdk": boolean,
 *   "autoplay": boolean,
 *   "ssl": boolean,
 *   "isshare": boolean,
 *   "viewability": boolean  // should player pause-play on viewability changes
 * }
 */
export default class Config {
    constructor(pui) {
        this.pui = pui;
        // protect from testing to null in every method
        this.__config = {
            ads: {},
            gui: {}
        };
        this.__shareUrl = null;
        this.__youtubeID = null;
    }

    init(externalConfig) {
        // merge external config with internal, if it was present
        this.__config = assign({}, this.__config, externalConfig);
        this.__config.ads = this.__config.ads || {};
        this.__config.gui = this.__config.gui || {};

        if (this.__config.adult == null) {
            this.__config.adult = false;
        }

        this.__shareUrl = __generateShareUrl(this.__config);
        this.__youtubeID = parseYoutubeID(this.getVideoFile());

        // init protocol for URL formatting of 'onload' and 'error' requests
        settings.urlProtocol = this.getProtocol();
    }

    getPUI() {
        return this.pui;
    }

    getShareUrl(pui) {
        if (pui) {
            return this.__shareUrl + '&pui=' + encodeURI(pui);
        }
        if (this.pui) {
            return this.__shareUrl + '&pui=' + encodeURI(this.pui);
        }
        return this.__shareUrl;
    }

    getShowAdUrl() {
        const url = URL.SHOW_AD +
            __urlParam('cr_video_autoplay', true, '?') +
            __urlParam('cr_adonly', true) +
            __urlParam('cr_adult', false) +
            __urlParam('pui', this.pui);
        return ensureProtocol(url, this.getProtocol());
    }

    getPosterUrl() {
        return this.__get('poster', '');
    }

    isFullscreenRestricted() {
        return this.__getB(['gui.no_fullscreen', 'gui.fullscreen']);
    }

    isMute() {
        return this.__get(['mute', 'muted']);
    }

    isAdult() {
        return this.__getB('adult');
    }

    isInApp() {
        const appBundle = this.__get('app_bundle', '');
        return typeof(appBundle) === 'string' && appBundle.length > 0;
    }

    isNoUI() {
        return this.__getB(['no_ui', 'gui.no_gui']);
    }

    isNoSharingUI() {
        return this.__getB('gui.no_sharing');
    }

    isMuteVisible() {
        return this.__getB('gui.mute_switch');
    }

    isAndroidSDK() {
        return this.__getB('android_sdk');
    }

    isAutoplay() {
        return  this.__getB(['video_autoplay', 'autoplay']);
    }

    isAutoGetAds() {
        const autoGetAd = this.__get('ads.auto_get_ad');
        return autoGetAd != null ? Boolean(autoGetAd) : true;
    }

    isAdOnly() {
        return this.__getB('ad_only');
    }

    isVPAID() {
        return this.__getB('is_vpaid');
    }

    shouldRespectViewability() {
        return this.__getB('viewability');
    }

    isSSL() {
        return this.__getB('ssl');
    }

    getProtocol() {
        const useSSL = this.__get('ssl');
        if (useSSL != null) {
            return useSSL ? 'https:' : 'http:';
        }
        return location.protocol;
    }

    hasPassback() {
        return Boolean(this.getPassback() || this.getPassbackJS() || this.getPassbackHTML());
    }

    getPassback() {
        return ensureProtocol(this.__get('passback'), this.getProtocol());
    }

    getPassbackJS() {
        return this.__get('passback_js');
    }

    getPassbackHTML() {
        return this.__get('passback_html');
    }

    getSkipOffset() {
        return this.__get('ads.skipoffset');
    }

    getWidth() {
        return this.__get('width');
    }

    getHeight() {
        return this.__get('height');
    }

    getYoutubeID() {
        return this.__youtubeID;
    }

    getVideoFile() {
        return this.__get(['video_url', 'file'], '');
    }

    getSiteUrl() {
        const siteUrl = this.__get('site_url');
        if (siteUrl != null) {
            return siteUrl;
        }

        if (document.referrer.length > 0 && this.__get('is_share') === true) {
            return document.referrer;
        }

        return window.location.toString();
    }

    getSource() {
        return this.isInApp() ? this.__get('app_bundle') : this.getSiteUrl();
    }

    getFetchVastUrl() {
        return this.__get('ads.vast_url');
    }

    getProvidedVastXML() {
        return this.__get('ads.vast_xml');
    }

    generateVastTagUrl(rect) {
        let vastUrl;

        const isInApp = this.isInApp();
        // browser language
        const lang = (navigator.language || '').substr(0, 2) || 'en';

        // determine base url
        if (settings.isDev) {
            vastUrl = settings.vast_url;
        } else {
            const subDomain = (settings.env && ('.' + settings.env)) || (!isInApp && 'web') || '';
            vastUrl = this.getProtocol() + '//vast' + subDomain + '.' + VAST_TAG_DOMAIN + '/vmvast.php';
        }

        vastUrl += __urlParam('type', isInApp ? 1 : 0, '?');
        vastUrl += __urlParam('app_type', settings.getAppType());
        vastUrl += __urlParam('pui', this.pui);
        vastUrl += __urlParam('ua', navigator.userAgent);
        vastUrl += __urlParam('siteurl', this.getSiteUrl());
        vastUrl += __urlParam('player_width', rect.width);
        vastUrl += __urlParam('player_height', rect.height);
        vastUrl += __urlParam('video_title', this.__get('title', ''));
        vastUrl += __urlParam('video_desc', this.__get('description', '').slice(0, 80));
        vastUrl += __urlParam('video_autoplay', this.isAutoplay());
        vastUrl += __urlParam('video_url', this.getVideoFile());
        vastUrl += __urlParam('protocol', 'full');
        vastUrl += __urlParam('user_lng', lang);

        // params for mobile application in which player wrapped
        vastUrl += __joinParams(this.__config, ['app_bundle', 'app_name', 'app_store']);
        // params fo mobile traffic
        vastUrl += __joinParams(this.__config, ['didsha1', 'didmd5', 'dpidsha1', 'dpidmd5', 'macsha1', 'macmd5']);
        vastUrl += __joinParams(this.__config, ['aid', 'did', 'mac', 'instl', 'custompubid', 'custompubid2', 'custompubid3']);
        vastUrl += __joinParams(this.__config, ['user_yob', 'user_gen', 'coppa']);
        vastUrl += __joinParams(this.__config, ['lat', 'lon']);
        vastUrl += __joinParams(this.__config, ['lmt', 'dnt']);

        // add random as last param, for easier testing
        vastUrl += '&rnd=' + Math.floor(Math.random() * 1000000);

        return vastUrl;
    }

    getParentElement() {
        const element = this.__get('parent_element');
        if (element) {
            return element;
        }

        const id = this.__get('parent_id');
        if (id) {
            return document.getElementById(id);
        }

        return null;
    }

    getCompanionConfig() {
        return this.__get('ads.companion_div');
    }

    getVideoElement() {
        return this.__get('video_tag');
    }

    getVastRequestOptions() {
        return this.__get('request.vast');
    }

    fillNotifyProps(data) {
        data.pui = this.getPUI();
        data.src = this.getSource();
        data.w = this.getWidth();
        data.h = this.getHeight();
        data.ssl = this.isSSL();
        data.autoplay = Boolean(this.isAutoplay());
        data.mute = Boolean(this.isMute());
    }

    __get(propNames, defaultValue) {
        return get(this.__config, propNames, defaultValue);
    }

    __getB(propNames, defaultValue) {
        return Boolean(this.__get(propNames, defaultValue));
    }
}

function __generateShareUrl(config) {
    let result = '';

    for (let propName in config) {
        if (!config.hasOwnProperty(propName)) {
            continue;
        }

        if (typeof config[propName] !== 'object') {
            if (propName !== 'parentId' && propName !== 'video_autoplay') {
                if (typeof config[propName] === 'string') {
                    result += 'cr_' + propName + '=\'' + encodeURI(config[propName].replace(/\\/g, '\\\\')).replace(/'/g, '\\%27').replace(/&/g, '%26') + '\'';
                } else {
                    result += 'cr_' + propName + '=' + config[propName];
                }
                result += '&';
            }
        }
    }
    result += 'cr_video_autoplay=true&cr_isshare=true';
    return result;
}

function __joinParams(config, params) {
    params = params || [];

    return params.reduce((result, param) => {
        return result + __urlParam(param, config[param]);
    }, '');
}

function __urlParam(name, value, separator) {
    if (value == null || value === '') {
        return '';
    }
    return (separator || '&') + name + '=' + encodeURIComponent(value);
}



