import VPAIDFLASHClient from 'vendor/vpaid-flash-client/js/VPAIDFLASHClient';
import { PLAYER_EVENTS, VAST_EVENTS, MEDIA_EVENTS, MIME, URL } from '@/data/constants';
import EventSystem from '@/system/eventSystem';
import { showContextMenuAt } from '@/ui/contextMenu';
import { createVastError, VAST_ERRORS } from '@/system/errors';

const FlashClientOpts = { data: URL.VPAID_FLASH };
const EVENTS = [
    'AdLoaded',
    'AdStarted',
    'AdStopped',
    'AdSkipped',
    'AdSkippableStateChange', // VPAID 2.0 new event
    'AdSizeChange', // VPAID 2.0 new event
    'AdLinearChange',
    'AdDurationChange', // VPAID 2.0 new event
    'AdExpandedChange',
    'AdRemainingTimeChange', // [Deprecated in 2.0] but will be still fired for backwards compatibility
    'AdVolumeChange',
    'AdImpression',
    'AdVideoStart',
    'AdVideoFirstQuartile',
    'AdVideoMidpoint',
    'AdVideoThirdQuartile',
    'AdVideoComplete',
    'AdClickThru',
    'AdInteraction', // VPAID 2.0 new event
    'AdUserAcceptInvitation',
    'AdUserMinimize',
    'AdUserClose',
    'AdPaused',
    'AdPlaying',
    'AdLog',
    'AdError'
];
const VPAID_LOAD_TIMEOUT = 7100;
const CHECK_TIME_INTERVAL = 500;

export class VPAIDPlayer extends EventSystem {
    constructor(parent, videoEl, wap) {
        super();

        this._src = '';
        this._videoEl = videoEl;
        this._paused = true;
        this._duration = 0;
        this._currentTime = 0;
        this._id = Math.floor(Math.random() * 1000);
        this._parent = parent;
        this._volume = videoEl.volume;
        this._wap = wap;
        this._type = MIME.JS;
        this._errorSent = false;
        this._frameContainer = null;

        this.buffered = {
            get length() {
                return 0;
            }
        };
    }

    createElement() {
        console.log('[alfa] Create VPAID container element ', this._type);

        this._errorSent = false;

        // set VPAID load timeout handler
        this._adLoadTimeoutTimer = setTimeout(() => {
            console.log('[alfa] VPAID adloadtimeout');
            this._emitError('adloadtimeout');
        }, VPAID_LOAD_TIMEOUT);

        let frame;
        if (this._type === MIME.JS) {
            frame = this.createHTML5Frame();
        } else if (this._type === MIME.FLASH) {
            frame = this.createFlashFrame();
        }

        if (frame) {
            frame.style.border = '0px';
            frame.style.width = '100%';
            frame.style.height = '100%';
            frame.style.position = 'absolute';
            frame.style.left = '0';
            frame.style.top = '0';
            frame.style.overflow = 'hidden';
            frame.scrolling = 'no';
        }
    }

    createHTML5Frame() {
        // create FRAME and inject it to page
        const frame = this._frame = document.createElement('iframe');
        frame.src = 'javascript:false';
        this._parent.appendChild(frame);

        // write to frame source
        const d = frame.contentWindow.document;
        try {
            d.open().write(generateHTML5FrameSource(this._src));
            frame.contentWindow.alfaLoad = this.onIFrameReady.bind(this);
            frame.contentWindow.alfaError = (error) => this._emitError(error);
            d.close();
        } catch (e) {
            console.log(e);
        }

        // pass 'mousemove' event out of the frame
        frame.contentWindow.addEventListener('mousemove', (event) => {
            const boundingClientRect = frame.getBoundingClientRect();

            const evt = new CustomEvent('mousemove', { bubbles: true, cancelable: false });
            evt.clientX = event.clientX + boundingClientRect.left;
            evt.clientY = event.clientY + boundingClientRect.top;

            frame.dispatchEvent(evt);
        });

        // catch 'contextmenu' event, and show
        frame.contentWindow.document.addEventListener('contextmenu', (e) => {
            console.log('[alfa] ASD CONTEXT MENUS');
            let items = [
                { id: 'about_player', text: 'About Web Alfa player', href: 'http://www.alfaplayer.com/share/' }
            ];
            showContextMenuAt(e.x, e.y, items, this._parent);
            e.preventDefault();
        });

        return frame;
    }

    createFlashFrame() {
        let frame;

        if (this._frameContainer === null) {
            frame = this._frameContainer = document.createElement('span');
            this._frameContainer.className = 'alfa-vpaid-container';
            //this._frameContainer.style.display = 'none';
            this._parent.appendChild(this._frameContainer);
        }

        const vpaidFlashClient = new VPAIDFLASHClient(this._frameContainer, (error) => {
            if (error) {
                console.log('[alfa] VPAID Error:', error);
                this._emitError(error);
            }

            vpaidFlashClient.loadAdUnit(this._src, (error, adUnit) => {
                if (error) {
                    console.log('[alfa] VPAID Error:', error);
                    this._emitError(error);
                }

                this._creative = adUnit;
                const rect = this._parent.getBoundingClientRect();
                adUnit.initAd(rect.width, rect.height, 'normal', -1, {
                    AdParameters: this._wap.currentAd.adParameters || ''
                });

                EVENTS.forEach((eventName) => {
                    if (typeof(this['on' + eventName]) === 'function') {
                        adUnit.on(eventName, this['on' + eventName].bind(this));
                    } else {
                        adUnit.on(eventName, () => console.log(`[alfa VPAID] ${ eventName }`));
                    }
                });
            });
        }, FlashClientOpts);

        return frame;
    }

    show() {
        // do nothing
    }

    hide() {
        // do nothing
    }

    mute(value) {
        // do nothing
    }

    hasDuration() {
        return !isNaN(this.duration);
    }

    isEnded() {
        return this.hasDuration() && this.currentTime >= this.duration;
    }

    play() {
        console.log('[alfa] play VPAID ad');
        if (this._creative) {
            this._creative.resumeAd();
        }
    }

    pause() {
        console.log('[alfa] pause VPAID ad');
        if (this._creative) {
            this._creative.pauseAd();
        }
    }

    skipAd() {
        console.log('[alfa] skip VPAID ad');
        if (this._creative) {
            this._creative.skipAd();
        }
    }

    resizeAd(width, height, viewMode) {
        console.log('[alfa] resize VPAID ad');
        if (this._creative) {
            this._creative.resizeAd(width, height, viewMode);
        }
    }

    expandAd() {
        console.log('[alfa] expand VPAID ad');
        if (this._creative) {
            this._creative.expandAd();
        }
    }

    collapseAd() {
        console.log('[alfa] collapse VPAID ad');
        if (this._creative) {
            this._creative.collapseAd();
        }
    }

    getAdLinear() {
        if (this._creative) {
            return this._creative.getAdLinear();
        }
        return true;
    }

    getAdExpanded() {
        if (this._creative) {
            return this._creative.getAdExpanded();
        }
        return false;
    }

    getAdSkippableState() {
        if (this._creative) {
            return this._creative.getAdSkippableState();
        }
        return false;
    }

    get paused() {
        return this._paused;
    }

    get type() {
        return this._type;
    }

    set type(value) {
        this._type = value;
    }

    get volume() {
        return this._volume;
    }

    set volume(value) {
        console.log(`set volume: ${ value }`);

        this._volume = value;
        try {
            if (this._creative)
                this._creative.setAdVolume(value);
        } catch (e) {
            //console.error('VPAID error',e.message);
        }
    }

    get error() {
        return null;
    }

    get duration() {
        return this._duration;
    }

    get currentTime() {
        return this._currentTime;
    }

    //not implemented
    get src() {
        return this._src;
    }

    set src(value) {
        this._src = value;
        this.createElement();
    }

    onAdImpression() {
        console.log('[alfa] VPAID on AdImpression');
        this._wap.notifyImpressions();
    }

    onAdVideoFirstQuartile() {
        console.log('[alfa] VPAID on AdVideoFirstQuartile');
        this._wap.notifyOnce(VAST_EVENTS.FIRST_QUARTILE);
    }

    onAdVideoMidpoint() {
        console.log('[alfa] VPAID on AdVideoMidpoint');
        this._wap.notifyOnce(VAST_EVENTS.MIDPOINT);
    }

    onAdVideoThirdQuartile() {
        console.log('[alfa] VPAID on AdVideoThirdQuartile');
        this._wap.notifyOnce(VAST_EVENTS.THIRD_QUARTILE);
    }

    onAdLoaded() {
        this._creative.startAd();
        console.log('[alfa] VPAID on AdLoaded');

        if (this._adLoadTimeoutTimer) {
            clearTimeout(this._adLoadTimeoutTimer);
        }
        this._duration = 0;
        this._currentTime = 0;
        this._paused = false;
        this.emit(MEDIA_EVENTS.play);

        // set interval to repeatedly check and update VPAID duration and time in player interface
        this._checkTimeInterval = setInterval(() => {
            this._readDurationAndTime();
        }, CHECK_TIME_INTERVAL);

        try {
            this._creative.setAdVolume(this._volume);
        } catch (e) {
            // nothing to do
        }
    }

    onAdVideoStart() {
        console.log('[alfa] VPAID on AdVideoStart');
        this._paused = false;
        this._wap.notifyOnce(VAST_EVENTS.START);
    }

    onAdClickThru(url, id, playerHandles) {
        console.log('[alfa] VPAID on AdClickThru');
        this.emit(PLAYER_EVENTS.AD_CLICK_THROUGH, url, id, playerHandles);
    }

    onAdExpandedChange() {
        this._wap.emit(PLAYER_EVENTS.VPAID_EVENT, 'AdExpandedChange');
    }

    onAdInteraction() {
        this._wap.emit(PLAYER_EVENTS.VPAID_EVENT, 'AdInteraction');
    }

    onAdUserAcceptInvitation() {
        this._wap.emit(PLAYER_EVENTS.VPAID_EVENT, 'AdUserAcceptInvitation');
    }

    onAdUserMinimize() {
        this._wap.emit(PLAYER_EVENTS.VPAID_EVENT, 'AdUserMinimize');
    }

    onAdUserClose() {
        this._wap.emit(PLAYER_EVENTS.VPAID_EVENT, 'AdUserClose');
    }

    onAdSkippableStateChange() {
        this._wap.emit(PLAYER_EVENTS.VPAID_EVENT, 'AdSkippableStateChange');
    }

    onAdVideoComplete() {
        console.log('[alfa] VPAID on AdVideoComplete');
        this._wap.notifyOnce(VAST_EVENTS.COMPLETE);
    }

    onAdPaused() {
        console.log('[alfa] VPAID on AdPaused');
        this._paused = true;
        this.emit(MEDIA_EVENTS.pause);
    }

    onAdPlaying() {
        console.log('[alfa] VPAID on AdPlaying');
        this._paused = false;
        this.emit(MEDIA_EVENTS.play);
        this.emit(MEDIA_EVENTS.timeupdate);
    }

    onAdError(e) {
        console.log('[alfa] VPAID on AdError: ', e);
        this._paused = true;
        if (this._type === MIME.FLASH) {
            this._parent.removeChild(this._frameContainer);
            this._frameContainer = null;
        } else {
            this._parent.removeChild(this._frame);
        }
        this._frame = void(0);
        this._creative = void(0);
        if (this._adLoadTimeoutTimer) {
            clearTimeout(this._adLoadTimeoutTimer);
        }
        if (this._checkTimeInterval) {
            clearInterval(this._checkTimeInterval);
        }
        if (!this._errorSent)
            this._errorSent = true;

        const vastError = createVastError(VAST_ERRORS.VPAID_ERROR, e && e.message || e);
        this.emit(MEDIA_EVENTS.error, vastError);
    }

    onAdVolumeChange() {
        console.log('[alfa] VPAID on AdVolumeChange');
        this._readVolume();
    }

    onAdRemainingTimeChange() {
        this._readDurationAndTime();
    }

    onAdDurationChange() {
        this._readDurationAndTime();
    }

    onAdStopped() {
        console.log('[alfa] VPAID on AdStopped');

        if (this._checkTimeInterval) {
            clearInterval(this._checkTimeInterval);
        }

        if (this._type === MIME.FLASH) {
            this._parent.removeChild(this._frameContainer);
            this._frameContainer = null;
        } else {
            if (this._frame.parentNode)
                this._frame.parentNode.removeChild(this._frame);
        }

        setTimeout(() => this.emit(MEDIA_EVENTS.ended), 75);
        this._frame = void(0);
        this._creative = void(0);
    }

    onAdSkipped() {
        this._wap.emit(PLAYER_EVENTS.VPAID_EVENT, 'onAdSkipped');
    }

    _emitError(error) {
        if (!this._errorSent) {
            this._errorSent = true;

            const vastError = createVastError(VAST_ERRORS.VPAID_ERROR, error && error.message || error);
            this.emit(MEDIA_EVENTS.error, vastError);
        }
    }

    _readVolume() {
        if (this._type === MIME.FLASH) {
            this._creative.getAdVolume((err, value) => {
                if (err) {
                    return;
                }

                this._wap.ui.setVolumeUI(value);
                this._wap.emit(MEDIA_EVENTS.volumechange, value);
            });
        } else {
            const vol = this._creative.getAdVolume();
            this._wap.ui.setVolumeUI(vol);
            this._wap.emit(MEDIA_EVENTS.volumechange, vol);
        }
    }

    _readDurationAndTime() {
        if (!this._creative) {
            return;
        }

        if (this._type === MIME.FLASH) {
            this._creative.getAdDuration((err, value) => {
                if (err) {
                    return;
                }

                this._duration = value;
                this.emit(MEDIA_EVENTS.durationchange);
            });

            this._creative.getAdRemainingTime((err, value) => {
                if (err) {
                    return;
                }

                if (this._duration <= 0) {
                    this._duration = value;
                    this.emit(MEDIA_EVENTS.durationchange);
                }

                this._currentTime = this._duration - value;
                this.emit(MEDIA_EVENTS.timeupdate);
            });
        }
        else if (this._type === MIME.JS) {
            this._duration = this._creative.getAdDuration();
            this._currentTime = this._duration - this._creative.getAdRemainingTime();
            this.emit(MEDIA_EVENTS.timeupdate);
            this.emit(MEDIA_EVENTS.durationchange);
        }
    }

    onParentResize() {
        console.log('[alfa] OnParentResize');
        if (this._creative) {
            let rect = this._videoEl.getBoundingClientRect();
            this._creative.resizeAd(rect.width, rect.height, 'normal');
        }
    }

    onIFrameReady() {
        console.log('[alfa] VPAID IFRAME ready');
        const vpaidAD = this._creative = this._frame.contentWindow.getVPAIDAd();

        const adEl = this._frame.contentWindow.document.createElement('div');
        adEl.className = 'ad-element';
        adEl.style.height = '100%';
        this._frame.contentWindow.document.body.appendChild(adEl);

        EVENTS.forEach((eventName) => {
            const handler = this['on' + eventName];
            if (typeof(handler) === 'function') {
                vpaidAD.subscribe(handler, eventName, this);
            } else {
                vpaidAD.subscribe(() => console.log(`[alfa VPAID] ${ eventName }`), eventName, this);
            }
        });

        const rect = this._videoEl.getBoundingClientRect();
        vpaidAD.initAd(rect.width, rect.height, 'normal', 1000,
            {
                AdParameters: this._wap.currentAd.adParameters || ''
            },
            {
                slot: adEl,
                videoSlot: this._videoEl
            });
    }
}

function generateHTML5FrameSource(src) {
    return '<head>' +
        '<script>' +
        'function onLoad() {' +
        '    var js = document.createElement("script");' +
        '    js.src = "' + encodeURI(src) + '";' +
        '    js.addEventListener("load", function() { window.alfaLoad(); });' +
        '    js.addEventListener("error", function(error) { window.alfaError(new URIError("VPAID script " + error.target.src + " is not accessible.")); });' +
        '    document.body.appendChild(js);' +
        '}' +
        '</script>' +
        '</head>' +
        '<body onload="onLoad()" style="margin: 0">';
}



