import { MEDIA_EVENTS } from '@/data/constants';

export function parseTime(timeString) {
    if (!timeString) {
        return 0;
    }

    const msIndex = timeString.indexOf('.');
    if (msIndex > 0) {
        timeString = timeString.slice(0, msIndex);
    }

    const ar = timeString.split(':');
    let result = 0;
    if (ar.length === 3) {
        result = parseInt(ar[2]) + parseInt(ar[1]) * 60 + parseInt(ar[0]) * 3600;
    } else if (ar.length === 2) {
        result = parseInt(ar[1]) + parseInt(ar[0]) * 60;
    } else {
        return 0;
    }
    return result;
}

export function formatTime(seconds) {
    // handle invalid times
    if (seconds == null || seconds < 0 || isNaN(seconds) || seconds === Infinity) {
        return '-';
    }

    let s = Math.floor(seconds % 60),
        m = Math.floor(seconds / 60 % 60),
        h = Math.floor(seconds / 3600);

    h = h > 0 ? h + ':' : '';
    m = (m < 10 ? '0' + m : m) + ':';
    s = (s < 10) ? '0' + s : s;

    return h + m + s;
}

export function getUrlContentAsync(url, callback) {
    if (location.protocol === 'https:' && url.toString().substr(0, 5) === 'http:') {
        console.error('Incorrect URL protocol. Expected to be HTTPS request');
        return callback();
    }

    try {
        const xhr = new XMLHttpRequest();
        xhr.open('GET', url, true);

        xhr.onload = function () {
            if (xhr.readyState === 4) {
                if (xhr.status === 200) {
                    callback(xhr.responseText);
                } else {
                    callback();
                }
            }
        };

        xhr.onerror = function (e) {
            console.error(e.message);
            callback();
        };

        xhr.send();
    } catch (e) {
        console.error(e.message);
        callback();
    }
}

export function parseXML(source) {
    source = (source || '').trim();

    const parser = new DOMParser();
    const xml = parser.parseFromString(source, 'text/xml');

    // check if parser done without errors
    const parserErrorTag = xml.querySelector('parsererror');
    if (parserErrorTag) {
        throw new Error(parserErrorTag.innerText);
    }

    return xml;
}

export function getCDATA(xmlNode) {
    if (xmlNode && xmlNode.firstChild) {
        if (xmlNode.firstChild.wholeText) {
            return xmlNode.firstChild.wholeText.trim();
        }
        return xmlNode.firstChild.data;
    }

    return '';
}

export function playWhenCan(videoElement, successCallback, errorCallback) {
    // check if video is already buffered enough to start playing ...
    if (videoElement.readyState >= videoElement.HAVE_FUTURE_DATA) {
        runPlay(successCallback, errorCallback);
    } else {
        // ... if not - try to run play to overcome play-on-touch problem on mobile devices and additionally set callback on CANPLAY event
        runPlay();
        videoElement.addEventListener(MEDIA_EVENTS.canplay, onCanPlay);
    }

    function onCanPlay() {
        runPlay();
        videoElement.removeEventListener(MEDIA_EVENTS.canplay, onCanPlay);
    }

    function runPlay(onSuccess, onError) {
        onSuccess = onSuccess || noop;
        onError = onError || noop;

        if (!videoElement.paused) {
            return;
        }

        const promise = videoElement.play();
        if (promise) {
            promise
                .then(onSuccess)
                .catch(onError);
        } else {
            setTimeout(() => {
                // if video is paused, then probably there was an error
                if (videoElement.paused && onError) {
                    onError();
                }
            }, 100);
        }
    }
}

export function noop() {

}



