import { isUrl } from 'alfa-common/lib/utils/url';
import { assign } from 'alfa-common/lib/polyfills/assign';

import { doGet } from '@/system/networking';
import { settings } from '@/data/settings';
import { PLAYER_EVENTS } from '@/data/constants';
import { Flags } from '@/utils/flags';
import { Stage, STAGES } from '@/utils/stage';
import { createError, NoAdsError } from '@/system/errors';
import { Vast } from '@/parser/data';
import { STYLES } from '@/ui/styles';

const
    TIMEOUT_BETWEEN_BACKUPS = 10 * 60 * 60 * 1000, // ms, 10 hours
    TIMEOUT_BETWEEN_ADS = 10 * 60 * 1000, // ms, 10 minutes
    TIMEOUT_REQUEST = 3 * 1000, // ms, 3 seconds
    TIMEOUT_BETWEEN_REQUESTS = 2 * 1000, // ms, 2 seconds
    TIMEOUT_CHECK_AUTOPLAY = 1 * 1000, // ms, 1 seconds
    SHOW_AD_ON_START = true,
    SHOW_AD_ON_VIDEO_PLAY = true,
    AD_REQUESTS_LIMIT = 500;

export const FLAGS = {
    INITIAL: 'initial',
    GETTING_AD: 'getting_ad',
    PARSING_VAST: 'parsing_vast',
    PLAY_AD_ASAP: 'play_ad_asap',
    HAVE_AD_IN_CACHE: 'have_ad_in_cache'
};

export default class Scheduler {
    constructor(wap) {
        this.wap = wap;

        this.stage = new Stage();

        this._flags = new Flags(FLAGS.INITIAL);  // start with INITIAL state
        this.preCachedAd = false;

        this.timeBackupLastTried = 0;
        this.timeLastAdRequest = 0;
        this.timeLastAdFinished = 0;
        this.adCount = 0;
    }

    isGettingAd() {
        return this._flags.has(FLAGS.GETTING_AD);
    }

    willPlayAdASAP() {
        return this._flags.has(FLAGS.PLAY_AD_ASAP);
    }

    generateVastTagUrl() {
        return this.wap.config.generateVastTagUrl(this.wap.getPlayerSize());
    }

    precacheAd() {
        if (SHOW_AD_ON_START && this.wap.config.isAutoGetAds()) {
            const url = this.generateVastTagUrl();
            this.requestAd(url);
        }
    }

    playNextAd() {
        if (this.preCachedAd) {
            this.wap.playCurrentAd();
            return;
        }

        this.wap.showWaitingForAd();

        this._flags.include(FLAGS.PLAY_AD_ASAP);
        if (this._flags.has(FLAGS.GETTING_AD)) {
            return;
        }

        this.stage.next(STAGES.BEGIN);
        this.wap.abortPlaying();

        const url = this.generateVastTagUrl();
        this.requestAd(url);
    }

    // TODO: refactor this method
    tryBackupVast() {
        console.log('[alfa] try backup vast url');

        this._flags.include(FLAGS.PLAY_AD_ASAP, FLAGS.GETTING_AD);
        const config = this.wap.config;

        if (!settings.isRotated) {
            const currTime = +new Date;

            const passbackURL = config.getPassback();
            const passbackJS = config.getPassbackJS();
            const passbackHTML = config.getPassbackHTML();

            if (passbackURL && this.isTimeoutBetweenBackupsExpired(currTime)) {
                this.timeBackupLastTried = currTime;
                this.requestAd(passbackURL);
                return;
            } else if (passbackHTML) {
                const parentElem = config.getParentElement() || document.body;
                if (parentElem) {
                    injectPassbackHTML(parentElem, passbackHTML);
                    return;
                }
            } else if (passbackJS) {
                const parentElem = config.getParentElement() || document.body;
                if (parentElem) {
                    injectPassbackJS(parentElem, passbackJS);
                    return;
                }
            }
        } else if (this.adCount < AD_REQUESTS_LIMIT) {
            setTimeout(() => {
                const url = this.generateVastTagUrl();
                this.requestAd(url);
            }, TIMEOUT_BETWEEN_REQUESTS);
            return;
        }

        // if passback vast is not found, try to play main video, if it's not playing already
        if (this._flags.has(FLAGS.PLAY_AD_ASAP) && !this.stage.isVideo()) {
            if (this.shouldPlayMainVideo()) {
                this.wap.playMainVideo();
            } else {
                this.wap.ui.showError('Cannot play ad file');
            }
        }

        this._flags.exclude(FLAGS.INITIAL, FLAGS.GETTING_AD, FLAGS.PLAY_AD_ASAP);

        this.wap.emit(PLAYER_EVENTS.EMPTY_ADS_LIST);
    }

    shouldPlayAd(currTime) {
        // if we already playing AD, do not start another one
        if (this.stage.isAd()) {
            return false;
        }

        return this.wap.config.isAutoGetAds()
            && this.isTimeoutBetweenAdsExpired(currTime)
            && this.isAdRequestTimeExpired(currTime);
    }

    shouldPlayNextAd(currTime, currentVideoTime, duration) {
        // we should request second ad only for ROTATED build
        if (!settings.isRotated) {
            return false;
        }

        if (this.preCachedAd || this._flags.has(FLAGS.GETTING_AD, FLAGS.PARSING_VAST)) {
            return false;
        }

        if (this.stage.isAd()) {
            return !this.preCachedAd
                && (currentVideoTime > 0)
                && (duration > 0)
                && (duration - currentVideoTime < 2.0);
        } else {
            return this.isAdRequestTimeExpired(currTime);
        }
    }

    shouldPlayMainVideo() {
        return !this.wap.config.isAdOnly();
    }

    isAdRequestTimeExpired(time) {
        return (time - this.timeLastAdRequest) > TIMEOUT_BETWEEN_REQUESTS;
    }

    isTimeoutBetweenAdsExpired(time) {
        return (time - this.timeLastAdFinished) > TIMEOUT_BETWEEN_ADS;
    }

    isTimeoutBetweenBackupsExpired(time) {
        return (time - this.timeBackupLastTried) > TIMEOUT_BETWEEN_BACKUPS;
    }

    onTimeUpdate(currentVideoTime, duration) {
        if (this.adCount >= AD_REQUESTS_LIMIT) {
            return;
        }

        const currTime = +new Date;
        if (SHOW_AD_ON_VIDEO_PLAY && this.shouldPlayAd(currTime)) {
            console.log('[alfa] We should play FIRST ad');

            //this.wap.ui.player.pause();
            this._flags.include(FLAGS.PLAY_AD_ASAP);
            //this.wap.showWaitingForAd();

            const url = this.generateVastTagUrl();
            this.requestAd(url);

        } else if (this.shouldPlayNextAd(currTime, currentVideoTime, duration)) {
            console.log('[alfa] We should play NEXT ad');

            this.preCachedAd = false;
            this._flags.include(FLAGS.PLAY_AD_ASAP);

            const url = this.generateVastTagUrl();
            this.requestAd(url);
        }
    }

    onPlay() {
        // play main video, if we reached AD count limit
        if (this.adCount >= AD_REQUESTS_LIMIT) {
            this.wap.playMainVideo();
            return;
        }

        if (this._flags.has(FLAGS.INITIAL) && (this.wap.config.isAutoGetAds() || settings.isRotated) && SHOW_AD_ON_START) {
            console.log('[alfa] Scheduler onPlay, initialState');

            if (this._flags.has(FLAGS.GETTING_AD)) {
                this._flags.include(FLAGS.PLAY_AD_ASAP);
                this.wap.showWaitingForAd();
            } else if (this.wap.haveAdTags()) {
                this.wap.playCurrentAd(STAGES.PRE_ROLL);
            } else {
                console.log('[alfa] doGetAd onPlay');
                this.wap.showWaitingForAd();
                this._flags.include(FLAGS.PLAY_AD_ASAP);

                const url = this.generateVastTagUrl();
                this.requestAd(url);
            }
        } else {
            if (settings.isRotated) {
                this.stage.next(this.stage.previous);

                if (this._flags.has(FLAGS.GETTING_AD)) {
                    this._flags.include(FLAGS.PLAY_AD_ASAP);
                    return;
                }
            }
            if (this.stage.isAd()) {
                this.wap.playCurrentAd();
            } else {
                this.wap.playMainVideo();
            }
        }

        this._flags.exclude(FLAGS.INITIAL);
    }

    onAdFinished() {
        this.timeLastAdFinished = +new Date;
    }

    onAdSkipped() {
        this.timeLastAdFinished = +new Date;
    }

    requestAd(url) {
        console.log('[alfa] Scheduler.requestAd', url);

        this._flags.include(FLAGS.GETTING_AD);
        this.timeLastAdRequest = +new Date;
        this.adCount++;

        const requestOptions = {
            timeout: TIMEOUT_REQUEST,
            headers: { 'Content-Type': 'text/plain' }
        };

        // if we have additional headers in config
        if (process.env.ALFA_DEBUG) {
            const configOptions = this.wap.config.getVastRequestOptions();
            if (configOptions) {
                if (configOptions.headers) {
                    assign(requestOptions.headers, configOptions.headers);
                }
            }
        }

        doGet(url, requestOptions, (error, res) => {
            console.log(`[alfa] Scheduler.requestAd finished in ${ (+new Date - this.timeLastAdRequest) / 1000 }s`, url);

            // if main video already finished, don't try to play AD
            if (this.wap.state.finishedMain) {
                this._flags.exclude(FLAGS.PLAY_AD_ASAP);
                return;
            }

            if (error || !res || !(res.length > 0) || !Vast.looksLikeVast(res)) {
                // send error report to server
                this.sendNoAdsError(error, res);

                console.log('[alfa] No Ad available', res);
                this.wap.emit(PLAYER_EVENTS.EMPTY_ADS_LIST);
                this.tryBackupVast();
                return;
            }

            this._flags.exclude(FLAGS.GETTING_AD);
            this._flags.include(FLAGS.PARSING_VAST);

            this.wap.initVast(res, () => {
                this._flags.exclude(FLAGS.PARSING_VAST);

                if (this.wap.haveAdTags()) {
                    this.wap.setCurrentAd(0);

                    const firstAdTag = this.wap.getAdTags()[0];
                    if (firstAdTag.mediaFile) {
                        if (this._flags.has(FLAGS.PLAY_AD_ASAP) && (!settings.isRotated || this.wap.isPaused() || this._flags.has(FLAGS.INITIAL))) {
                            this._flags.exclude(FLAGS.PLAY_AD_ASAP);
                            this.wap.playCurrentAd();
                        } else {
                            this.preCachedAd = true;
                        }

                        // report successful ad acquiring
                        if (process.env.ALFA_DEBUG && settings.isRotated) {
                            this.wap.notifyInfo('rotated_ad');
                        }
                    } else {
                        this.wap.playMainVideo();
                    }
                } else {
                    this.wap.playMainVideo();
                }
            });
        });
    }

    onMainFinishedEvent() {
        /*
         if(this.postBreak) {
         this.wap.vastData = this.postBreak.vastData;
         this.wap.setCurrentAd(0);
         this.wap.playCurrentAd(STAGES.POST_ROLL);
         utils.notifyUrls(this.postBreak.events['breakstart']);
         }
         */
    }

    sendNoAdsError(requestError, responseData) {
        let message;
        if (requestError) {
            message = 'Error in getting VAST: ' + requestError && requestError.message || requestError;
        } else if (responseData && responseData.length > 0 && !Vast.looksLikeVast(responseData)) {
            message = `Response data doesn't looks like VAST: "${ responseData.slice(0, 100) }"`;
        } else {
            message = 'Empty VAST response';
        }

        if (this.wap.config.hasPassback()) {
            message += ' >> Trying passback';
        }

        const error = new NoAdsError(message);
        this.wap.notifyError(error);
    }

    /**
     * Check if ad was successfully started by autoplay - report error otherwise
     */
    watchForAutoplay() {
        const checkAutoplay = () => {
            if (this.wap.state.initial) {
                this.wap.notifyError(createError('Autoplay is not working'));
            }
        };

        setTimeout(checkAutoplay, TIMEOUT_CHECK_AUTOPLAY);
    }
}

function injectPassbackHTML(rootElement, passbackURL) {
    if (!rootElement) {
        return;
    }

    // remove all elements from 'rootElement'
    while (rootElement.firstChild) {
        rootElement.removeChild(rootElement.firstChild);
    }

    // create FRAME and inject it to page
    const frame = document.createElement('iframe');
    frame.src = passbackURL;
    frame.style.cssText = STYLES.PASSBACK_HTML_IFRAME;
    frame.scrolling = 'no';
    rootElement.appendChild(frame);
}

function injectPassbackJS(rootElement, passbackJS) {
    if (!rootElement) {
        return;
    }

    // remove all elements from 'rootElement'
    while (rootElement.firstChild) {
        rootElement.removeChild(rootElement.firstChild);
    }

    // if 'passbackJS' is element already, then just add it to 'rootElement'
    if (typeof(passbackJS) === 'object') {
        rootElement.appendChild(passbackJS);
        return;
    }

    // ... else, create 'script' element and include JS source in it
    const jsNode = document.createElement('script');
    if (isUrl(passbackJS)) {
        jsNode.src = passbackJS;
    } else {
        // if JS source is enclosed in <script> tags, then remove them
        if (passbackJS.substr(0, 7) === '<script') {
            passbackJS = passbackJS.replace(/<\s*\/?\s*script\s*.*?>/g, '');
        }
        jsNode.innerHTML = passbackJS;
    }
    rootElement.appendChild(jsNode);
}



