import EventSystem from '@/system/eventSystem';
import { createVastError, VAST_ERRORS } from '@/system/errors';
import { STYLES } from '@/ui/styles';

export const EVENTS = {
    VIEW: 'view',
    CLOSE: 'close',
    CONTENT_CLICK: 'content_click',
    VAST_ERROR: 'vast_error'
};

export class Overlay extends EventSystem {
    constructor(nonLinear) {
        super();

        this.parent = null;
        this.element = null;
        this.nonLinear = nonLinear;
    }

    create(parent) {
        this.parent = parent;

        const element = this.element = document.createElement('div');
        element.className = 'alfa-overlay-root';

        // hide root overlay element, until successful image loading
        this.hide();

        // close button click listener
        const closeButton = document.createElement('div');
        closeButton.className = 'alfa-overlay-close-btn';
        closeButton.addEventListener('click', () => {
            this.emit(EVENTS.CLOSE);
        });

        const contentFrame = document.createElement('div');
        contentFrame.className = 'alfa-overlay-content-frame';

        // banner image
        const image = document.createElement('img');

        // banner image props
        image.src = this.nonLinear.staticResource;
        image.style.width = '100%';
        image.style.height = '100%';

        // show banner after successful image loading
        image.onload = () => {
            this.show();
            this.emit(EVENTS.VIEW);
        };

        // emit VAST error if target resource cannot be loaded (404 error)
        image.onerror = () => {
            const error = createVastError(VAST_ERRORS.NONLINEAR_FETCH, `NonLinear resource '${image.src}' is not accessible.`);
            this.emit(EVENTS.VAST_ERROR, error);
        };

        // content click listener
        image.addEventListener('click', () => {
            window.open(this.nonLinear.clickThrough, '_blank');
            this.emit(EVENTS.CONTENT_CLICK, this.nonLinear);
        });
        contentFrame.appendChild(image);

        element.appendChild(contentFrame);
        element.appendChild(closeButton);

        // update banner sizes according to NonLinear size, with parent size in mind
        this.updateGeometry();

        parent.appendChild(element);
    }

    /**
     * Update banner sizes according to NonLinear size, with parent size in mind
     */
    updateGeometry() {
        const availableWidth = this.parent.offsetWidth - 30;
        const availableHeight = this.parent.offsetHeight - 50;
        const scaleWidth = availableWidth / this.nonLinear.width;
        const scaleHeight = availableHeight / this.nonLinear.height;

        const minScale = scaleHeight < scaleWidth ? scaleHeight : scaleWidth;

        this.width = minScale * this.nonLinear.width;
        this.height = minScale * this.nonLinear.height;

        this.element.style.width = this.width + 'px';
        this.element.style.height = this.height + 'px';
        this.element.style.bottom = '40px';
        this.element.style.left = Math.round((this.parent.offsetWidth - this.width) / 2) + 'px';
    }

    show() {
        if (this.element) {
            this.element.classList.remove(STYLES.OPACITY_HIDDEN);
        }
    }

    hide() {
        if (this.element) {
            this.element.classList.add(STYLES.OPACITY_HIDDEN);
        }
    }

    remove() {
        if (this.element && this.element.parentNode) {
            this.element.parentNode.removeChild(this.element);
        }
    }
}



