import { PLAYER_EVENTS, VAST_EVENTS } from '@/data/constants';
import { parsedUA as ua } from '@/data/userAgent';
import EventSystem from '@/system/eventSystem';
import { getCDATA, parseXML, getUrlContentAsync, parseTime } from '@/utils/common';
import { checkHttpsAwareness } from '@/utils/url';
import { createVastError, VAST_ERRORS } from '@/system/errors';
import { Vast, AdTag, Linear, MediaFile, Companion, NonLinear, Icon } from './data';

const WRAPPER_LOOP_LIMIT = 10;

export default class ParserVast2 extends EventSystem {
    constructor(version) {
        super();

        this.version = version;
        this.wrapperCounter = 0;
        this.sources = [];
    }

    parse(xml, source, callback) {
        this.sources.push(source);

        const ads = xml.getElementsByTagName('Ad');
        if (ads.length === 0) {
            return callback(createVastError(VAST_ERRORS.UNDEFINED_ERROR, 'No AD tags in VAST'));
        }

        const vast = new Vast(this.version);
        let adTagIndex = 0;

        const readTagFunc = () => {
            // console.log('Parse AD:', ads[iteration]);

            this.readAdTag(ads[adTagIndex], new AdTag(), true, (error, adTag) => {
                if (error) {
                    return callback(error);
                }

                // merge events from linear tracking tags
                adTag.mergeLinearEvents();

                // select appropriate media file based on user agent and screen size
                const screenSize = {
                    width: window.screen.availWidth,
                    height: window.screen.availHeight
                };
                adTag.selectMediaFile(ua, screenSize);

                // notify server about absence of supported MediaFile, if there was any MediaFiles
                if (adTag.linears.length > 0 && !adTag.linear) {
                    this.__emitVastWarning(VAST_ERRORS.LINEAR_NOT_SUPPORTED, 'No supported Linear MediaFile');
                }

                // take only ads with impressions or VPAID mediafiles
                if (adTag.impressions.length > 0 || (adTag.mediaFile && adTag.mediaFile.isVPAID())) {
                    vast.adTags.push(adTag);
                }

                // count ads
                adTagIndex++;

                // if there is another Ad - parse it
                if (adTagIndex < ads.length) {
                    return readTagFunc();
                }

                // notify about VAST without impressions
                if (vast.adTags.length === 0) {
                    let message = 'There was no ADs with impressions in VAST';
                    if (this.wrapperCounter > 0) {
                        message += `, after following wrappers for ${ this.wrapperCounter } times`;
                    }

                    return callback(createVastError(VAST_ERRORS.SCHEMA_ERROR, message, source));
                }

                // store last parsed XML
                vast.source = this.sources.length > 0 ? this.sources[this.sources.length - 1] : '';

                // end of parsing and return to caller
                callback(null, vast);
            });
        };

        readTagFunc();
    }

    readAdTag(adNode, adTag, followAdditionalWrappers, callback) {
        const inlineTags = adNode.getElementsByTagName('InLine');
        const wrapperTags = adNode.getElementsByTagName('Wrapper');
        const inInLine = inlineTags.length > 0;
        const adChildTag = inInLine ? inlineTags[0] : wrapperTags[0];

        const adSystemTags = adChildTag.getElementsByTagName('AdSystem');
        if (adSystemTags.length > 0) {
            adTag.adSystem = getCDATA(adSystemTags[0]);
        }

        const errorTags = adChildTag.getElementsByTagName('Error');
        if (errorTags.length > 0) {
            adTag.error = getCDATA(errorTags[0]);
        }

        const impressionTags = adChildTag.getElementsByTagName('Impression');
        for (let i = 0; i < impressionTags.length; i++) {
            const impressionText = getCDATA(impressionTags[i]);
            if (impressionText) {
                adTag.impressions.push(impressionText);
            }
        }

        const creativeTags = adChildTag.getElementsByTagName('Creative');
        for (let i = 0; i < creativeTags.length; i++) {
            const linearTags = creativeTags[i].getElementsByTagName('Linear');
            for (let j = 0; j < linearTags.length; j++) {
                const linear = this.__readLinearTag(linearTags[j], inInLine);
                adTag.linears.push(linear);
            }

            const companionTags = creativeTags[i].getElementsByTagName('Companion');
            if (companionTags.length > 0) {
                for (let j = 0; j < companionTags.length; j++) {
                    let companion = __readCompanionTag(companionTags[j]);
                    adTag.companions.push(companion);
                }
            }

            const nonLinearAdsTags = creativeTags[i].getElementsByTagName('NonLinearAds');
            if (nonLinearAdsTags.length > 0) {
                __readTrackingArray(adTag.eventsNonLinear, nonLinearAdsTags[0].getElementsByTagName('Tracking'));

                const nonLinearTags = nonLinearAdsTags[0].getElementsByTagName('NonLinear');
                for (let j = 0; j < nonLinearTags.length; j++) {
                    const nl = this.__readNonLinearTag(nonLinearTags[j]);

                    // report that we don't support current 'NonLinear' type
                    if (!nl.staticResource) {
                        const types = [];
                        if (nl.htmlResource) {
                            types.push('html');
                        }
                        if (nl.iframeResource) {
                            types.push('iframe');
                        }
                        this.__emitVastWarning(VAST_ERRORS.NONLINEAR_SUPPORT, 'Not found supported STATIC NonLinear, found: ' + types.join(','));
                    }

                    adTag.nonLinears.push(nl);
                }
            }
        }

        // <Icons> -> <Icon> tags
        const iconTags = adChildTag.getElementsByTagName('Icon');
        for (let i = 0; i < iconTags.length; i++) {
            const icon = this.__readIconTag(iconTags[i]);
            icon.index = adTag.icons.length;
            adTag.icons.push(icon);
        }

        if (followAdditionalWrappers && wrapperTags && wrapperTags.length > 0) {
            // if we beyond max limit, then follow wrapper
            if (this.wrapperCounter < WRAPPER_LOOP_LIMIT) {
                this.wrapperCounter++;
                const followAdditionalWrappersAttr = wrapperTags[0].getAttribute('followAdditionalWrappers');
                const followNext = !(followAdditionalWrappersAttr === 'false' || followAdditionalWrappersAttr === false);

                try {
                    this.followWrapper(adTag, wrapperTags[0], followNext, callback);
                    return;
                } catch (e) {
                    // notify listeners about wrapper error
                    const source = this.sources.length > 0 ? this.sources[this.sources.length - 1] : '';
                    return callback(createVastError(VAST_ERRORS.SCHEMA_ERROR, e.message, source));
                }
            } else {
                // notify server about wrapper limit error
                const source = this.sources.length > 0 ? this.sources[this.sources.length - 1] : '';
                return callback(createVastError(VAST_ERRORS.WRAPPER_LIMIT, 'Exceeded max wrapper limit: ' + WRAPPER_LOOP_LIMIT, source));
            }
        }

        callback(null, adTag);
    }

    followWrapper(adTag, wrapper, followAdditionalWrappers, callback) {
        const tagURI = wrapper.getElementsByTagName('VASTAdTagURI');

        // return if absent or empty 'VASTAdTagURI' tag
        if (tagURI.length <= 0 || tagURI[0].childNodes.length <= 0) {
            callback(null, adTag);
            return;
        }

        const url = checkHttpsAwareness(tagURI[0].childNodes[0].wholeText.trim());
        getUrlContentAsync(url, (source) => {
            // check for empty response
            if (!source) {
                return callback(createVastError(VAST_ERRORS.SCHEMA_ERROR, 'Empty VAST returned by wrapper from url: ' + url, source));
            }

            // collect VAST sources
            this.sources.push(source);

            // try to parse response as XML
            let xml;
            try {
                xml = parseXML(source);
            } catch (e) {
                return callback(createVastError(VAST_ERRORS.PARSING_ERROR, 'Error while parsing VAST returned by wrapper from url "' + url + '": ' + e.message, source));
            }

            // recursively parse Ad
            const ads = xml.getElementsByTagName('Ad');
            if (ads && ads.length > 0) {
                return this.readAdTag(ads[0], adTag, followAdditionalWrappers, callback);
            }

            // notify server about wrapper response without ads
            this.__emitVastWarning(VAST_ERRORS.WRAPPER_NO_ADS, 'No ADS in wrapper response on url: ' + url);
            // return to current wrapper
            callback(null, adTag);
        });
    }

    __readLinearTag(tag, inInLine) {
        const linear = new Linear();

        // some data can be only in 'InLine' tag (not in 'Wrapper')
        if (inInLine) {
            // duration
            const durationTags = tag.getElementsByTagName('Duration');
            if (durationTags.length > 0) {
                const durationText = getCDATA(durationTags[0]);
                linear.duration = parseTime(durationText);
            }

            // skip offset
            linear.skipOffset = __readOffset(tag.getAttribute('skipoffset'), linear.duration);

            // media files
            const mediaFileTags = tag.getElementsByTagName('MediaFile');
            for (let i = 0; i < mediaFileTags.length; i++) {
                const mediaFile = this.__readMediaFile(mediaFileTags[i]);
                linear.mediaFiles.push(mediaFile);
            }

            // click through
            const clickThrough = tag.getElementsByTagName('ClickThrough');
            if (clickThrough.length > 0) {
                linear.clickThrough = getCDATA(clickThrough[0]);
            }

            // ad parameters
            const adParameters = tag.getElementsByTagName('AdParameters');
            if (adParameters.length > 0) {
                linear.parameters = getCDATA(adParameters[0]);
            }
        }

        // tracking events
        __readTrackingArray(linear.events, tag.getElementsByTagName('Tracking'), linear.duration);

        // clicks tracking
        const clickTracking = tag.getElementsByTagName('ClickTracking');
        for (let i = 0; i < clickTracking.length; i++) {
            linear.clickTrackings.push(getCDATA(clickTracking[i]));
        }

        // custom clicks
        const customClick = tag.getElementsByTagName('CustomClick');
        for (let i = 0; i < customClick.length; i++) {
            linear.clickCustoms.push(getCDATA(customClick[i]));
        }

        return linear;
    }

    __readMediaFile(tag) {
        const mediaFile = new MediaFile();
        mediaFile.delivery = tag.getAttribute('delivery').toLowerCase().trim();
        mediaFile.type = tag.getAttribute('type').toLowerCase().trim();
        mediaFile.width = tag.getAttribute('width') * 1;
        mediaFile.height = tag.getAttribute('height') * 1;
        mediaFile.bitrate = tag.getAttribute('bitrate') * 1;
        mediaFile.url = getCDATA(tag);
        mediaFile.apiFramework = tag.getAttribute('apiFramework');
        return mediaFile;
    }

    __readNonLinearTag(tag) {
        const nl = new NonLinear();

        const staticResource = tag.getElementsByTagName('StaticResource');
        if (staticResource.length > 0) {
            nl.staticResource = getCDATA(staticResource[0]);
            nl.type = staticResource[0].getAttribute('creativeType');
        }

        const htmlResource = tag.getElementsByTagName('HTMLResource');
        if (htmlResource.length > 0) {
            nl.htmlResource = getCDATA(htmlResource[0]);
        }

        const iframeResource = tag.getElementsByTagName('IFrameResource');
        if (iframeResource.length > 0) {
            nl.iframeResource = getCDATA(iframeResource[0]);
        }

        const nonLinearClickThrough = tag.getElementsByTagName('NonLinearClickThrough');
        if (nonLinearClickThrough.length > 0) {
            nl.clickThrough = getCDATA(nonLinearClickThrough[0]);
        }

        const nonLinearClickTracking = tag.getElementsByTagName('NonLinearClickTracking');
        if (nonLinearClickTracking.length > 0) {
            nl.clickTracking = getCDATA(nonLinearClickTracking[0]);
        }

        nl.height = tag.getAttribute('height') * 1;
        nl.width = tag.getAttribute('width') * 1;

        return nl;
    }

    __readIconTag(tag) {
        const icon = new Icon();

        // required props
        icon.program = tag.getAttribute('program');
        icon.width = tag.getAttribute('width') * 1;
        icon.height = tag.getAttribute('height') * 1;
        icon.xPosition = readPosition(tag.getAttribute('xPosition'));
        icon.yPosition = readPosition(tag.getAttribute('yPosition'));
        icon.resource = null;

        // resources
        const staticResource = tag.getElementsByTagName('StaticResource');
        if (staticResource.length > 0) {
            icon.staticResource = getCDATA(staticResource[0]);
            icon.staticResourceType = staticResource[0].getAttribute('type');
        }

        const htmlResource = tag.getElementsByTagName('HTMLResource');
        if (htmlResource.length > 0) {
            icon.htmlResource = getCDATA(htmlResource[0]);
        }

        const iframeResource = tag.getElementsByTagName('IFrameResource');
        if (iframeResource.length > 0) {
            icon.iframeResource = getCDATA(iframeResource[0]);
        }

        // optional props
        icon.apiFramework = tag.getAttribute('apiFramework');
        icon.offset = parseTime(tag.getAttribute('offset'));
        icon.duration = parseTime(tag.getAttribute('duration'));

        // clicks and tracking
        const viewTrackingTags = tag.getElementsByTagName('IconViewTracking');
        for (let i = 0; i < viewTrackingTags.length; i++) {
            icon.viewTracking.push(getCDATA(viewTrackingTags[i]));
        }
        const clickThroughTags = tag.getElementsByTagName('IconClickThrough');
        if (clickThroughTags && clickThroughTags.length > 0) {
            icon.clickThrough = getCDATA(clickThroughTags[0]);
        }
        const clickTrackingTags = tag.getElementsByTagName('IconClickTracking');
        for (let i = 0; i < clickTrackingTags.length; i++) {
            icon.clickTracking.push(getCDATA(clickTrackingTags[i]));
        }

        return icon;

        function readPosition(value) {
            if (value === 'left' || value === 'right' || value === 'top' || value === 'bottom') {
                return value;
            }
            const num = value * 1;
            return num || 0;
        }
    }

    __emitVastWarning(code, msg) {
        const warning = createVastError(code, msg);
        this.emit(PLAYER_EVENTS.VAST_WARNING, warning);
    }
}

function __readOffset(text, duration) {
    if (!text) {
        return 0;
    }

    // if offset is set in percents
    if (text.indexOf('%') > 0) {
        if (duration > 0) {
            const skipOffsetPercent = text.slice(0, -1) * 1;
            return duration * skipOffsetPercent / 100;
        }
    } else {
        return parseTime(text);
    }
}

function __readCompanionTag(tag) {
    const companion = new Companion();

    const companionClickThrough = tag.getElementsByTagName('CompanionClickThrough');
    if (companionClickThrough.length > 0) {
        companion.clickThrough = getCDATA(companionClickThrough[0]);
    }

    const staticResource = tag.getElementsByTagName('StaticResource');
    if (staticResource.length > 0) {
        companion.staticResource = getCDATA(staticResource[0]);
        companion.creativeType = staticResource[0].getAttribute('creativeType');
    }

    const htmlResource = tag.getElementsByTagName('HTMLResource');
    if (htmlResource.length > 0) {
        companion.htmlResource = getCDATA(htmlResource[0]);
    }

    const iframeResource = tag.getElementsByTagName('IFrameResource');
    if (iframeResource.length > 0) {
        companion.iframeResource = getCDATA(iframeResource[0]);
    }

    __readTrackingArray(companion.events, tag.getElementsByTagName('Tracking'));

    companion.width = tag.getAttribute('width') * 1;
    companion.height = tag.getAttribute('height') * 1;

    return companion;
}

function __readTrackingArray(intoTarget, trackingTags, duration) {
    for (let j = 0; j < trackingTags.length; j++) {
        let eventName = trackingTags[j].getAttribute('event');
        if (!eventName) {
            continue;
        }

        // lowercasing all event names
        eventName = eventName.toLowerCase();
        if (!intoTarget[eventName]) {
            intoTarget[eventName] = [];
        }

        // 'progress' event also contains 'offset' attribute
        if (eventName === VAST_EVENTS.PROGRESS) {
            intoTarget[eventName].push({
                offset: __readOffset(trackingTags[j].getAttribute('offset'), duration),
                url: getCDATA(trackingTags[j])
            });
        } else {
            intoTarget[eventName].push(getCDATA(trackingTags[j]));
        }
    }
}



