import { uuid } from 'alfa-common/lib/utils/uuid';
import { ensureProtocol } from 'alfa-common/lib/utils/url';
import { URL, DEFAULTS } from '@/data/constants';
import { Storage } from '@/data/storage';

const KEY_VOLUME = 'AlfaPlayerVolume';
const KEY_CURRENT_PLAY = 'alfa_current_play';
const KEY_ENV = 'alfa_env';
const KEY_VAST_URL = 'alfa_vast_url';

// this SID used in 'load' event and for first player instance
export const INITIAL_SID = uuid();
let isInitialSIDUsed = false;

export class Settings {
    constructor() {
        this.isRotated = false;
        this.urlProtocol = '';
        this.storage = new Storage();
    }

    get env() {
        return this.storage.read(KEY_ENV) || '';
    }

    get vast_url() {
        return this.storage.read(KEY_VAST_URL) || URL.VAST_BASE_DEV;
    }

    get volume() {
        const str = this.storage.read(KEY_VOLUME);
        if (!str) {
            return 1;
        }
        const num = +str;
        return !isNaN(num) ? num : 1;
    }

    set volume(value) {
        this.storage.write(KEY_VOLUME, value);
    }

    get currentPlay() {
        const num = parseFloat(this.storage.read(KEY_CURRENT_PLAY));
        return !isNaN(num) ? num : 0;
    }

    set currentPlay(value) {
        this.storage.write(KEY_CURRENT_PLAY, value);
    }

    get skipAdOffset() {
        return this.isDev ? 1 : DEFAULTS.SKIP_AD_TIME;
    }

    get isDev() {
        return this.env === 'dev';
    }

    nextSID() {
        if (!isInitialSIDUsed) {
            isInitialSIDUsed = true;
            return INITIAL_SID;
        } else {
            return uuid();
        }
    }

    getShortenerUrl() {
        return this.isDev ? URL.SHORTENER_URL_DEV : URL.SHORTENER_URL;
    }

    getAppType() {
        const APP_NAME = 'webplayer';

        if (process.env.ALFA_ROTATED) {
            return APP_NAME + '.r';
        } else if (process.env.ALFA_VPAID) {
            return APP_NAME + '.vpaid';
        }
        return APP_NAME;
    }

    getNotifyOnLoadUrl() {
        const url = this.isDev ? URL.NOTIFY_ON_LOAD_DEV : URL.NOTIFY_ON_LOAD;
        return ensureProtocol(url, this.urlProtocol);
    }

    getNotifyInfoUrl() {
        const url = this.isDev ? URL.NOTIFY_INFO_DEV : URL.NOTIFY_INFO;
        return ensureProtocol(url, this.urlProtocol);
    }

    getNotifyErrorUrl() {
        const url = this.isDev ? URL.NOTIFY_ERROR_DEV : URL.NOTIFY_ERROR;
        return ensureProtocol(url, this.urlProtocol);
    }
}

export const settings = new Settings();



