const HISTORY_COUNT = 32;

export const STAGES = {
    BEGIN: 'begin',
    MAIN_VIDEO: 'mainvideo',
    PRE_ROLL: 'preroll',
    MID_ROLL: 'midroll',
    POST_ROLL: 'postroll',
    END: 'end'
};

export class Stage {
    constructor() {
        // start with BEGIN stage
        this._stages = [
            STAGES.BEGIN
        ];
    }

    get current() {
        return this._stages && this._stages.slice(-1)[0] || STAGES.BEGIN;
    }

    get previous() {
        return this._stages && this._stages.slice(-2, -1)[0] || STAGES.BEGIN;
    }

    next(stage) {
        // do not change anything, if stage is the same as current
        if (this.current === stage) {
            return;
        }

        // add current stage to stage history
        this._stages.push(stage);
        // reduce 'stages' count to limit
        this._stages = this._stages.slice(-1 * HISTORY_COUNT);
    }

    isBegin() {
        return this.current === STAGES.BEGIN;
    }

    isPreRoll() {
        return this.current === STAGES.PRE_ROLL;
    }

    isMidRoll() {
        return this.current === STAGES.MID_ROLL;
    }

    isPostRoll() {
        return this.current === STAGES.POST_ROLL;
    }

    isVideo() {
        return this.current === STAGES.MAIN_VIDEO;
    }

    isEnd() {
        return this.current === STAGES.END;
    }

    isAd() {
        return this.isPreRoll() || this.isMidRoll() || this.isPostRoll();
    }
}



