import { settings } from '@/data/settings';
import { sendInfo, sendOnLoad } from '@/system/networking';

function genBody(name, sid) {
    return {
        name: name,
        ver: process.env.VERSION || 'dev',
        type: settings.getAppType(),
        sid: sid || undefined
    };
}

export function notifyInfo(name, { sid, data }) {
    // request data
    const body = genBody(name || 'info', sid);
    body.data = data;

    // make request
    sendInfo(body);
}

export function notifyCreate({ sid, data }) {
    // request data
    const body = genBody('create', sid);
    body.data = data;

    // make request
    sendOnLoad(body);
}



