import EventSystem from '@/system/eventSystem';
import { css, STYLES } from '@/ui/styles';
import { parsedUA as ua } from '@/data/userAgent';
import { isChildOf } from './utils';
import { PLAYER_EVENTS } from '@/data/constants';

export const EVENTS = {
    MOUSE_OVER_PLAYER: 'mouse_over_player',
    MOUSE_OUT_OF_PLAYER: 'mouse_out_of_player'
};

export class Background extends EventSystem {
    constructor() {
        super();

        this.element = null;
    }

    create(parent, width, height) {
        const element = this.element = document.createElement('div');

        // click event
        element.addEventListener(ua.isMobile ? 'touchend' : 'click', (e) => this.emit(PLAYER_EVENTS.CLICK, e));
        // bind mouse events
        this.__bindMouseEvents(parent, element);

        element.className = STYLES.ROOT;
        if (parent) {
            element.classList.add('alfa-main-background');

            if (width && height) {
                parent.style.width = width + 'px';
                parent.style.height = height + 'px';
                element.style.width = width + 'px';
                element.style.height = height + 'px';
            } else if (parent.offsetHeight > 0 && parent.offsetWidth > 0) {
                element.style.width = parent.offsetWidth + 'px';
                element.style.height = parent.offsetHeight + 'px';
            } else {
                element.style.cssText = STYLES.BACKGROUND_FULL_PAGE;
            }
        } else {
            element.classList.add('alfa-video-background');
        }

        // load player styles
        const styleElement = document.createElement('style');
        styleElement.innerHTML = css;
        element.appendChild(styleElement);

        if (parent) {
            parent.appendChild(element);
        } else {
            document.body.appendChild(element);
        }
    }

    destroy() {
        this.element.parentNode.removeChild(this.ui.element);
    }

    addElement(child) {
        this.element.appendChild(child);
    }

    removeElement(child) {
        this.element.removeChild(child);
    }

    isFullscreen() {
        return this.element.classList.contains(STYLES.FULLSCREEN);
    }

    enterFullscreen() {
        this.element.classList.add(STYLES.FULLSCREEN);
    }

    exitFullscreen() {
        this.element.classList.remove(STYLES.FULLSCREEN);
    }

    /**
     * Set player background to full translucent, so we can see underneath player if we injected as VPAID player
     */
    enableTranslucence() {
        this.element.style.background = 'rgba(0,0,0,0)';
    }

    isInBounds(target) {
        return isChildOf(target, this.element);
    }

    get size() {
        return {
            width: this.element.offsetWidth,
            height: this.element.offsetHeight
        };
    }

    /**
     * Bind mouse events if NOT on mobile device
     * @param element
     * @private
     */
    __bindMouseEvents(parent, element) {
        // check for mobile device
        if (ua.isMobile) {
            return;
        }

        // bind 'mousemove' event
        element.addEventListener('mousemove', (e) => {
            this.emit(EVENTS.MOUSE_OVER_PLAYER, e);
        }, true);
        // bind 'mouseout' event
        element.addEventListener('mouseout', (e) => {
            // check if mouse is out of player bounds
            const root = parent || element;
            if (isChildOf(e.relatedTarget, root)) {
                return;
            }

            this.emit(EVENTS.MOUSE_OUT_OF_PLAYER, e);
        });
    }
}



