import { parsedUA as ua } from '@/data/userAgent';
import EventSystem from '@/system/eventSystem';
import { normalizePercent } from './utils';
import { STYLES } from '@/ui/styles';
import { formatTime } from '@/utils/common';

export const EVENTS = {
    SEEK_START: 'seek_start',
    SEEK: 'seek',
    SEEK_END: 'seek_end'
};

export class TimeBar extends EventSystem {
    constructor() {
        super();

        this.element = null;
        this.thumb = null;
        this.playRail = null;
        this.buffRail = null;
        this.time = null;

        this.__isSeeking = false;
    }

    create(parent) {
        const timeBar = this.element = document.createElement('span');
        timeBar.className = `${STYLES.TIMEBAR} ${STYLES.NO_SELECT}`;

        // seek rail
        const seekRail = document.createElement('span');
        seekRail.className = `${STYLES.TIMEBAR_RAIL} ${STYLES.TIMEBAR_SEEK_RAIL}`;
        const seekRailFilling = document.createElement('span');
        seekRailFilling.className = `${STYLES.TIMEBAR_RAIL_FILLING} ${STYLES.TIMEBAR_SEEK_RAIL_FILLING}`;
        seekRail.appendChild(seekRailFilling);

        // buff rail
        this.buffRail = document.createElement('span');
        this.buffRail.className = `${STYLES.TIMEBAR_RAIL} ${STYLES.TIMEBAR_BUFF_RAIL}`;
        this.buffRail.style.width = '0%';
        const buffRailFilling = document.createElement('span');
        buffRailFilling.className = `${STYLES.TIMEBAR_RAIL_FILLING} ${STYLES.TIMEBAR_BUFF_RAIL_FILLING} ${STYLES.NO_SELECT}`;
        this.buffRail.appendChild(buffRailFilling);

        // play rail
        this.playRail = document.createElement('span');
        this.playRail.className = `${STYLES.TIMEBAR_RAIL} ${STYLES.TIMEBAR_PLAY_RAIL}`;
        const playRailFilling = document.createElement('span');
        playRailFilling.className = `${STYLES.TIMEBAR_RAIL_FILLING} ${STYLES.TIMEBAR_PLAY_RAIL_FILLING} ${STYLES.NO_SELECT}`;
        this.playRail.appendChild(playRailFilling);

        this.thumb = document.createElement('span');
        this.thumb.className = `${STYLES.TIMEBAR_THUMB} ${STYLES.NO_SELECT}`;

        this.time = document.createElement('span');
        this.time.className = `${STYLES.TIMEBAR_TIME} ${STYLES.NO_SELECT}`;
        this.setTime(0);

        // append main elements
        timeBar.appendChild(seekRail);
        timeBar.appendChild(this.buffRail);
        timeBar.appendChild(this.playRail);
        timeBar.appendChild(this.thumb);
        timeBar.appendChild(this.time);

        parent.appendChild(timeBar);

        if (ua.isMobile) {
            timeBar.addEventListener('touchstart', (e) => this.__onSeekStart(e));
            timeBar.addEventListener('touchmove', (e) => this.__onSeek(e));
            timeBar.addEventListener('touchend', (e) => this.__onSeekEnd(e));
        } else {
            timeBar.addEventListener('mousedown', (e) => this.__onSeekStart(e));
            window.addEventListener('mousemove', (e) => this.__onSeek(e));
            window.addEventListener('mouseup', (e) => this.__onSeekEnd(e));
            timeBar.addEventListener('click', (e) => {
                e.preventDefault();
                e.stopPropagation();
            }, true);
        }
    }

    setPosition(percent) {
        this.setBufferedPosition(percent);
        this.setPlayPosition(percent);
    }

    setTime(time) {
        if (time > 0) {
            this.time.innerText = formatTime(time);
        } else {
            this.time.innerText = formatTime(0);
        }
    }

    getBufferedPosition() {
        return parseFloat(this.buffRail.style.width);
    }

    setBufferedPosition(percent) {
        this.buffRail.style.width = percent > 0 ? percent + '%' : '0';
    }

    getPlayPosition() {
        return parseFloat(this.playRail.style.width);
    }

    setPlayPosition(percent) {
        this.thumb.style.left = percent > 0 ? percent + '%' : '0';
        this.time.style.left = percent > 0 ? percent + '%' : '0';
        this.playRail.style.width = percent > 0 ? percent + '%' : '0';
    }

    show() {
        this.element.style.visibility = '';
    }

    hide() {
        this.element.style.visibility = 'hidden';
    }

    isSeeking() {
        return this.__isSeeking;
    }

    __onSeekStart() {
        console.log('[alfa] SeekStart');

        this.__isSeeking = true;
        this.emit(EVENTS.SEEK_START);
    }

    __onSeek(e) {
        if (!this.__isSeeking) {
            return;
        }

        if (ua.isMobile) {
            e = e.changedTouches[0];
        }

        const percent = this.__calculateSeekPercent(e);
        this.setPlayPosition(percent);
        this.emit(EVENTS.SEEK, percent);
    }

    __onSeekEnd(e) {
        if (!this.__isSeeking) {
            return;
        }

        console.log('[alfa] SeekEnd');
        this.__onSeek(e);
        e.stopPropagation();

        this.__isSeeking = false;
        this.emit(EVENTS.SEEK_END);
    }

    __calculateSeekPercent(e) {
        let rect = this.element.getBoundingClientRect();
        let percent = (e.clientX - rect.left) / this.element.offsetWidth;
        return normalizePercent(percent) * 100;
    }
}



