import { PLAYER_EVENTS } from '@/data/constants';
import EventSystem from '@/system/eventSystem';
import { parseXML } from '@/utils/common';
import { createVastError, VAST_ERRORS } from '@/system/errors';

import ParserVast1 from './parserVast1';
import ParserVast2 from './parserVast2';

export function parse(source, onVastWarning, callback) {
    let xml, firstTag;
    if (typeof source === 'object') {
        xml = source;
        firstTag = xml;
    } else {
        try {
            xml = parseXML(source);
            firstTag = xml.firstChild;
        } catch (e) {
            return callback(createVastError(VAST_ERRORS.PARSING_ERROR, 'Error while parsing VAST XML: ' + e.message), source);
        }
    }

    // determine VAST version by root tag
    let parser;
    if (firstTag.nodeName === 'VideoAdServingTemplate') {
        parser = new ParserVast1(1);
    } else if (firstTag.nodeName === 'VAST') {
        const ver = firstTag.getAttribute('version');
        if (ver.substr(0, 3) === '2.0') {
            parser = new ParserVast2(2);
        } else if (ver.substr(0, 3) === '3.0') {
            parser = new ParserVast2(3);
        } else if (ver.substr(0, 1) === '4') {
            parser = new ParserVast2(4);
        } else {
            return callback(createVastError(VAST_ERRORS.VERSION_NOT_SUPPORTED, `Version in VAST - ${ver}`), source);
        }
    } else {
        return callback(createVastError(VAST_ERRORS.SCHEMA_ERROR, `Not supported root tag - ${firstTag.nodeName}`), source);
    }

    // if we want to pass warnings and parser supports event emitting, then bind to it
    if (onVastWarning && parser instanceof EventSystem) {
        parser.on(PLAYER_EVENTS.VAST_WARNING, (error) => {
            onVastWarning(error);
        });
    }

    // run parsing
    parser.parse(xml, source, callback);
}



