export class Flags {
    constructor(...flags) {
        this.__flags = {};
        this.include(...flags);
    }

    include(...flags) {
        flags.forEach((flag) => {
            if ((flag != null) && !this.has(flag)) {
                this.__flags[flag] = true;
            }
        });
    }

    exclude(...flags) {
        flags.forEach((flag) => {
            if ((flag != null) && this.has(flag)) {
                this.__flags[flag] = false;
            }
        });
    }

    has(...flags) {
        for (let i = 0; i < flags.length; i++) {
            const flag = flags[i];
            if ((flag != null) && this.__flags[flag]) {
                return true;
            }
        }
        return false;
    }
}



