import { uniq } from 'alfa-common/lib/utils/array';
import { checkHttpsAwareness } from '@/utils/url';
import { VAST_EVENTS } from '@/data/constants';

const FLAG_IMPRESSIONS = '__impressions';

/**
 * Notify server through 'Image.src' hack.
 * It's main value is it not susceptible to CORS restrictions, unlike XHR requests.
 */
export class VastNotifier {
    constructor(bus) {
        this.onceGuard = {};
        this.pending = {};

        this.impressions = [];
        this.eventsLinear = null;
        this.eventsNonLinear = null;

        this.error = '';
    }

    init(ad) {
        this.onceGuard = {};  // clear notify once flags

        this.impressions = ad.impressions || [];
        this.eventsLinear = ad.eventsLinear || null;
        this.eventsNonLinear = ad.eventsNonLinear || null;

        this.error = ad.error || '';
    }

    /**
     * Fire event by name
     *
     * @param {string} eventName
     */
    fireLinear(eventName) {
        if (!this.eventsLinear) {
            return;
        }

        const urls = this.eventsLinear[eventName];

        console.log('[alfa] Notify event: ', eventName, urls);

        if (urls && urls.length > 0) {
            this.fireByUrl(urls);
        }
    }

    /**
     * Fire event by name only once for current ad and return was impressions fired on this call or not
     * @param {string} eventName
     * @returns {boolean} Was event fired on this call
     */
    fireLinearOnce(eventName) {
        if (!this.eventsLinear) {
            return false;
        }
        // if event was invoked earlier - do nothing
        if (this.wasFired(eventName)) {
            return false;
        }

        // Remember that event was invoked
        this.markAsFired(eventName);

        // Fire event
        this.fireLinear(eventName);

        return true;
    }

    fireProgress(time) {
        const progressEvents = this.eventsLinear[VAST_EVENTS.PROGRESS];
        if (!progressEvents || progressEvents.length === 0) {
            return;
        }

        // search appropriate 'progress' events
        const filteredEvents = progressEvents
        // take only 'not fired' before
            .filter((event, index) => !this.wasFired(__formatEventName(index)))
            // remove those that are early yet
            .filter((event) => event.offset <= time);

        filteredEvents.forEach((event) => {
            console.log(`[alfa] Notify progress event for ${ event.offset }s at ${ time }s: ${ event.url }`);

            // Remember that event with index from complete list was invoked
            this.markAsFired(__formatEventName(progressEvents.indexOf(event)));

            // Fire event
            this.fireByUrl(event.url);
        });

        function __formatEventName(index) {
            return VAST_EVENTS.PROGRESS + '_' + index;
        }
    }

    /**
     * Fire current ad impressions only once and return was impressions fired on this call or not
     *
     * @returns {boolean} Was impressions fired on this call
     */
    fireImpressions() {
        if (this.impressions.length === 0) {
            return false;
        }
        // if event was invoked earlier - do nothing
        if (this.wasFired(FLAG_IMPRESSIONS)) {
            return false;
        }

        // Remember that event was invoked
        this.markAsFired(FLAG_IMPRESSIONS);

        console.log('[alfa] Notify impressions:', this.impressions);
        this.fireByUrl(this.impressions);

        return true;
    }

    fireNonLinear(eventName) {
        if (!this.eventsNonLinear) {
            return;
        }

        const urls = this.eventsNonLinear[eventName];

        console.log('[alfa] Notify NonLinear event: ', eventName, urls);

        if (urls && urls.length > 0) {
            this.fireByUrl(urls);
        }
    }

    /**
     * Fire request to provided URL/URLs
     *
     * @param {String|String[]} urls URL
     */
    fireByUrl(urls) {
        if (!urls) {
            return;
        }

        // normalize and remove duplicates
        urls = normalizeUrls(urls);
        urls = uniq(urls);

        urls.forEach((url) => {
            url = checkHttpsAwareness(url);

            const rnd = Math.round(Math.random() * 100000);
            url = url + ((url.indexOf('?') > 0) ? '&' : '?') + 'r' + rnd + '=' + rnd;

            const img = document.createElement('img');
            img.src = url;

            // add IMG to PENDING object, to retain object reference, so GC doesn't delete it before request is over
            const imgCode = 'img_' + rnd;
            this.pending[imgCode] = img;
            img.onload = img.onerror = () => {
                delete this.pending[imgCode];
            };
        });
    }

    /**
     * Fire error by code
     *
     * @param {number} errorCode
     */
    fireError(errorCode) {
        if (!this.error) {
            return;
        }

        console.log('[alfa] Notify vast error:', errorCode);

        const url = this.error.replace(/\[ERRORCODE\]/g, errorCode);
        this.fireByUrl(url);
    }

    /**
     * Check if event was fired for current ad
     *
     * @param {string} eventName
     * @returns {boolean}
     */
    wasFired(eventName) {
        return this.onceGuard[eventName];
    }

    /**
     * Mark event as fired for current ad
     *
     * @param {string} eventName
     */
    markAsFired(eventName) {
        this.onceGuard[eventName] = true;
    }
}

function normalizeUrls(urls) {
    // make array from single url
    if (!Array.isArray(urls)) {
        urls = [urls];
    }

    return urls
        .map((url) => {
            // we can receive URL object with additional params, take only url path from it
            if (url && url.url) {
                return url.url;
            }
            if (typeof url === 'string') {
                return url;
            }
        })
        .filter((url) => url);
}



