import fscreen from 'fscreen';

import { PLAYER_EVENTS, MEDIA_EVENTS } from '@/data/constants';
import { parsedUA as ua } from '@/data/userAgent';
import { STYLES } from '@/ui/styles';
import { playWhenCan } from '@/utils/common';
import { VideoPlayer } from '@/players/video';
import { YoutubePlayer } from '@/players/youtube';
import { VPAIDPlayer } from '@/players/vpaid';
import { settings } from '@/data/settings';

import { Background, EVENTS as BACKGROUND_EVENTS } from './background';
import { IconsContainer, EVENTS as ICON_EVENTS } from './iconsContainer';
import { Overlay, EVENTS as OVERLAY_EVENTS } from './overlay';
import { SkipButton, EVENTS as SKIP_BUTTON_EVENTS } from './skipButton';
import { AdultDialog, EVENTS as ADULT_DIALOG_EVENTS } from './adultDialog';
import { ControlBar, EVENTS as CONTROLS_BAR_EVENTS } from './controlBar';
import { ShareButton, EVENTS as SHARE_BUTTON_EVENTS } from './shareButton';
import { ButtonMute, EVENTS as BUTTON_MUTE_EVENTS } from './buttonMute';
import { ShareDialog, EVENTS as SHARE_DIALOG_EVENTS } from './shareDialog';
import { showContextMenuAt } from './contextMenu';
import { isElementPartiallyInViewport } from './utils';
import { ViewabilityObserver, EVENTS as VIEWABILITY_EVENTS } from '../utils/viewability';

const CHECK_PLAYER_TIMEOUT = 5000;

export default class UIManager {
    constructor(wap) {
        this.wap = wap;
        this.randomId = Math.random();
        this._volume = 1;

        // root element
        this.background = new Background();
        // container for video players
        this.mediaSpan = null;

        // current video player
        this.player = null;
        // available players
        this.videoPlayer = null;
        this.audioPlayer = null;
        this.youtubePlayer = null;
        this.vpaidPlayer = null;

        // share button and hideable share panel
        this.shareButton = new ShareButton();
        this.shareDialog = new ShareDialog(false);

        this.buttonMute = new ButtonMute();

        this.videoPoster = null;
        this.loadingIndicator = null;
        this.skipButton = null;
        this.closeButton = null;
        this.nonLinear = null;
        this.buttonStart = null;

        // icons
        this.iconsContainer = new IconsContainer();

        // controls frame elements
        this.controlBar = new ControlBar();
        this.hideControlsTimer = null;

        // viewability observer
        this.viewability = null;

        this.onPlay = this.onPlay.bind(this);
        this.onPause = this.onPause.bind(this);
        this.onTimeUpdate = this.onTimeUpdate.bind(this);
        this.onProgress = this.onProgress.bind(this);
        this.onDurationChange = this.onDurationChange.bind(this);

        this.onPlayFinished = wap.onPlayFinished.bind(wap);
        this.onVideoError = wap.onVideoError.bind(wap);
    }

    createMainUI(config) {
        if (this.player || this.background.element) {
            return;
        }

        window.addEventListener('message', this.onWindowMessage.bind(this), false);
        document.addEventListener('contextmenu', this.onContextMenuRequested.bind(this), false);

        const background = this.background;
        background.create(config.getParentElement(), config.getWidth(), config.getHeight());
        background.on(BACKGROUND_EVENTS.MOUSE_OVER_PLAYER, this.onMouseOverPlayer.bind(this));
        background.on(BACKGROUND_EVENTS.MOUSE_OUT_OF_PLAYER, this.onMouseOutOfPlayer.bind(this));

        // init viewability observer
        this.initViewability(config);

        const mediaSpan = this.mediaSpan = document.createElement('span');
        mediaSpan.className = 'alfa-media-span';

        if (ua.isIOSSmall || ua.isUCBrowser) {
            mediaSpan.classList.add(STYLES.OPACITY_HIDDEN);
        }

        background.addElement(mediaSpan);

        this.createVideoPlayer(config);
        this.showVideoPlayer();

        if (config.getYoutubeID() && this.wap.isAllowYoutubeWrapper()) {
            this.createYoutubePlayer(config);
        }

        if (!ua.isMobile) {
            if (config.isMute() != null) {
                if (config.isMute())
                    this.setVolume(0);
                else
                    this.setVolume(1);
            } else if (!config.isVPAID()) {
                this.setVolume(settings.volume);
            }
        }

        // if adult flag is present, then show warning dialog before showing UI
        if (config.isAdult()) {
            this.showAdultDialog();  // call showUI() there, if user agrees with warning
        } else {
            this.showUI();
        }

        if (!config.isVPAID()) {
            setTimeout(() => this.onCheckCurrentPlayer(), CHECK_PLAYER_TIMEOUT);
        }
    }

    initViewability(config) {
        // viewability observer
        this.viewability = new ViewabilityObserver(this.background.element);

        // on VISIBLE event
        this.viewability.on(VIEWABILITY_EVENTS.VISIBLE, () => {
            // report player info (with viewability) to server - only on DEBUG build
            if (process.env.ALFA_DEBUG) {
                this.wap.notifyInfo('viewability');
            }

            // if allowed, we should PLAY on viewability change
            if (config.shouldRespectViewability()) {
                this.viewabilityPlay();
            }
        });

        // on HIDDEN event
        this.viewability.on(VIEWABILITY_EVENTS.HIDDEN, () => {
            // report player info (with viewability) to server - only on DEBUG build
            if (process.env.ALFA_DEBUG) {
                this.wap.notifyInfo('viewability');
            }

            // if allowed, we should PAUSE on viewability change
            if (config.shouldRespectViewability()) {
                this.viewabilityPause();
            }
        });
    }

    createVideoPlayer(config) {
        const videoPlayer = this.videoPlayer = new VideoPlayer();

        const configVideoElement = config.getVideoElement();
        if (configVideoElement) {
            console.log('[alfa] VideoPlayer will use VIDEO tag from config');

            videoPlayer.useElement(configVideoElement);
            // set player background to full translucent, so we can see underneath player if we injected as VPAID player
            this.background.enableTranslucence();
        } else {
            videoPlayer.createElement(this.mediaSpan, config);
            videoPlayer.element.addEventListener(MEDIA_EVENTS.canplay, () => {
                this.hideLoadingIndicator();
                if (videoPlayer.paused) {
                    this.showStartButton();
                }
            });
        }
    }

    createYoutubePlayer(config) {
        const youtube = this.youtubePlayer = new YoutubePlayer();
        youtube.includeYoutubeAPI();
        youtube.createElement(this.mediaSpan, config.getYoutubeID());

        youtube.on(MEDIA_EVENTS.timeupdate, this.onTimeUpdate);
        youtube.on(MEDIA_EVENTS.durationchange, this.onDurationChange);
        youtube.on(PLAYER_EVENTS.CLICK, this.wap.onPlayerClickMain.bind(this.wap));
        youtube.on(PLAYER_EVENTS.BUFFERING, this.onBuffering.bind(this));
    }

    createControls() {
        if (!this.background.element) {
            return;
        }

        // main controls bar
        this.controlBar.create(this.background.element, this.wap.config);
        this.controlBar.on(CONTROLS_BAR_EVENTS.PLAY_PAUSE_CLICK, this.onPlayPauseClick.bind(this));
        this.controlBar.on(CONTROLS_BAR_EVENTS.VOLUME_CLICK, this.onVolumeClick.bind(this));
        this.controlBar.on(CONTROLS_BAR_EVENTS.VOLUME_CHANGE, this.onVolumeChange.bind(this));
        this.controlBar.on(CONTROLS_BAR_EVENTS.FULLSCREEN_CLICK, this.onFullscreenClick.bind(this));
        this.controlBar.on(CONTROLS_BAR_EVENTS.TIME_SEEK_START, this.onTimeBarSeekStart.bind(this));
        this.controlBar.on(CONTROLS_BAR_EVENTS.TIME_SEEK, this.onTimeBarSeek.bind(this));
        this.controlBar.on(CONTROLS_BAR_EVENTS.TIME_SEEK_END, this.onTimeBarSeekEnd.bind(this));

        // fullscreen change event bindings
        if (fscreen.fullscreenEnabled) {
            fscreen.addEventListener('fullscreenchange', this.onFullscreenChange.bind(this));
        }

        this.listenVideoEvents();

        this.setVolumeUI(this.player.volume);

        // if we need to show separate mute switch
        if (this.wap.config.isMuteVisible()) {
            this.buttonMute.create(this.background.element);
            this.buttonMute.muted(this.wap.config.isMute());

            this.buttonMute.on(BUTTON_MUTE_EVENTS.CLICK, this.onVolumeClick.bind(this));
        }
    }

    listenVideoEvents() {
        const player = this.player;
        player.on(MEDIA_EVENTS.waiting, () => this.showLoadingIndicator());
        player.on(MEDIA_EVENTS.playing, () => this.hideLoadingIndicator());
        player.on(MEDIA_EVENTS.loadstart, () => {
            if (player.element.src !== '') {
                this.showLoadingIndicator();
            }
        });

        player.on(MEDIA_EVENTS.play, this.onPlay);
        player.on(MEDIA_EVENTS.pause, this.onPause);
        player.on(MEDIA_EVENTS.ended, this.onPlayFinished);
        player.on(MEDIA_EVENTS.error, this.onVideoError);

        player.on(MEDIA_EVENTS.timeupdate, this.onTimeUpdate);
        player.on(MEDIA_EVENTS.progress, this.onProgress);
        player.on(MEDIA_EVENTS.durationchange, this.onDurationChange);

        player.on(MEDIA_EVENTS.canplaythrough, this.onCanPlaythroughEvent.bind(this));
        player.on(MEDIA_EVENTS.loadstart, this.onLoadstartEvent.bind(this));
    }

    isInViewport() {
        return isElementPartiallyInViewport(this.background.element);
    }

    /**
     * Is element eligible to generate click through event
     *
     * @param {Element} element
     * @returns {boolean} Is valid
     */
    isValidForClickThrough(element) {
        return element === this.player.element
            || element === this.background.element
            || element === this.videoPoster
            || element === this.iconsContainer.element
            || element === this.mediaSpan;
    }

    seekToTime(time) {
        // 1 = HAVE_METADATA - metadata for the audio/video is ready
        if (this.player.readyState < 1) {
            const readyMetadata = () => {
                this.player.currentTime = time;
                this.player.off(MEDIA_EVENTS.loadedmetadata, readyMetadata);
            };
            this.player.on(MEDIA_EVENTS.loadedmetadata, readyMetadata);
        } else {
            this.player.currentTime = time;
        }
        this.wap.state.finishedMain = false;
    }

    showAdultDialog() {
        const dialog = new AdultDialog();
        dialog.create(this.background.element);
        dialog.on(ADULT_DIALOG_EVENTS.AGREED, () => {
            this.showUI();
            if (this.wap.config.isAutoplay()) {
                this.wap.play();
            }
        });
        dialog.on(ADULT_DIALOG_EVENTS.REJECT, () => {
            window.location = (document.referrer && document.referrer !== window.location) ? document.referrer : 'https://google.com';
        });
    }

    showUI() {
        this.createPoster();
        this.showStartButton();

        this.createSharingUI();

        this.createControls();
        this.showShareButton();

        if (this.wap.config.isNoUI()) {
            this.controlBar.hide(true);
        }
    }

    showControlBar() {
        if (this.wap.config.isNoUI()) {
            return;
        }

        this.controlBar.show();
        this.setupHideControlsTimer();
    }

    hideControlBar(isInstant) {
        this.controlBar.hide(isInstant);
    }

    isControlBarVisible() {
        return this.controlBar.isVisible();
    }

    setupHideControlsTimer(timeout) {
        if (ua.isMobile && this.wap.scheduler.stage.isAd() || this.wap.scheduler.stage.isBegin()) {
            return;
        }

        timeout = timeout || 2500;
        clearTimeout(this.hideControlsTimer);
        this.hideControlsTimer = setTimeout(() => {
            if (ua.isMobile && this.wap.scheduler.stage.isAd()) {
                return;
            }

            if (!this.player.paused) {
                this.controlBar.hide();
            }
        }, timeout);
    }

    /**
     * Pause and hide previous player
     */
    hidePreviousPlayer() {
        const player = this.player;
        if (!player) {
            return;
        }

        player.hide();
        player.off(MEDIA_EVENTS.play, this.onPlay);
        player.off(MEDIA_EVENTS.pause, this.onPause);
        player.off(MEDIA_EVENTS.ended, this.onPlayFinished);
        player.off(MEDIA_EVENTS.error, this.onVideoError);
        if (!player.paused) {
            player.pause();
        }
    }

    showYoutubePlayer() {
        const youtubePlayer = this.youtubePlayer;
        if (youtubePlayer && youtubePlayer === this.player) {
            return;
        }

        // pause and hide previous player
        this.hidePreviousPlayer();

        if (youtubePlayer) {
            console.log('[alfa] change active player to YoutubePlayer');

            youtubePlayer.show();
            youtubePlayer.on(MEDIA_EVENTS.play, this.onPlay);
            youtubePlayer.on(MEDIA_EVENTS.pause, this.onPause);
            youtubePlayer.on(MEDIA_EVENTS.ended, this.onPlayFinished);
            youtubePlayer.on(MEDIA_EVENTS.error, this.onVideoError);
            this.player = youtubePlayer;

            this.controlBar.totalTime.setTime(youtubePlayer.duration);
        } else {
            console.log('[alfa] change active player to null');

            this.player = null;
        }
    }

    showVideoPlayer() {
        const videoPlayer = this.videoPlayer;
        if (videoPlayer && videoPlayer === this.player) {
            return;
        }

        // pause and hide previous player
        this.hidePreviousPlayer();

        console.log('[alfa] change active player to VideoPlayer');

        videoPlayer.show();
        videoPlayer.on(MEDIA_EVENTS.play, this.onPlay);
        videoPlayer.on(MEDIA_EVENTS.pause, this.onPause);
        videoPlayer.on(MEDIA_EVENTS.ended, this.onPlayFinished);
        videoPlayer.on(MEDIA_EVENTS.error, this.onVideoError);
        this.player = videoPlayer;

        // TODO: Do we need this? Previous player should be hidden by now
        if (this.youtubePlayer) {
            this.youtubePlayer.hide();
        }
    }

    showVPAIDPlayer() {
        let vpaidPlayer = this.vpaidPlayer;

        // if VPAID player is created and active - exit
        if (vpaidPlayer && vpaidPlayer === this.player) {
            return;
        }

        // pause and hide previous player
        this.hidePreviousPlayer();

        // create VPAID player, if needed
        if (!vpaidPlayer) {
            if (ua.isMobile) {
                this.player.load();
            }
            vpaidPlayer = this.vpaidPlayer = new VPAIDPlayer(this.mediaSpan, this.videoPlayer.element, this.wap);
            vpaidPlayer.on(MEDIA_EVENTS.play, this.onPlay);
            vpaidPlayer.on(MEDIA_EVENTS.ended, this.onPlayFinished);
            vpaidPlayer.on(MEDIA_EVENTS.pause, this.onPause);
            vpaidPlayer.on(MEDIA_EVENTS.error, this.onVideoError);

            vpaidPlayer.on(MEDIA_EVENTS.timeupdate, this.onTimeUpdate);
            vpaidPlayer.on(MEDIA_EVENTS.durationchange, this.onDurationChange);

            vpaidPlayer.on(PLAYER_EVENTS.AD_CLICK_THROUGH, this.wap.onClickThrough.bind(this.wap));
            vpaidPlayer.on(PLAYER_EVENTS.AD_CLICK_THROUGH, () => {
                this.wap.emit(PLAYER_EVENTS.AD_CLICK_THROUGH);
            });
        }

        this.hidePoster();

        console.log('[alfa] change active player to VPAIDPlayer');

        vpaidPlayer.show();
        this.player = vpaidPlayer;
    }

    showShareDialog(shareUrl) {
        this.shareButton.hide();
        this.shareDialog.show(shareUrl);
    }

    morphToAdsLayout() {
        this.hideShareButton();
        this.controlBar.morphToAdsLayout();
        this.onTimeUpdate();
    }

    morphToWaitingAdsLayout() {
        this.morphToAdsLayout();
        this.showLoadingIndicator();

        this.controlBar.morphToWaitingAdsLayout();
    }

    morphToNormalLayout() {
        this.controlBar.morphToNormalLayout();
    }

    showCloseButton() {
        if (!this.closeButton) {
            let closeButton = this.closeButton = document.createElement('div');
            closeButton.className = 'alfa-button-close';
            closeButton.innerHTML = '&#xd7;';
            this.background.addElement(closeButton);
            closeButton.addEventListener('click', this.wap.onCloseClick.bind(this.wap));
        }
    }

    hideCloseButton() {
        if (this.closeButton) {
            this.background.removeElement(this.closeButton);
            this.closeButton = null;
        }
    }

    showLoadingIndicator() {
        if (this.wap.config.isAndroidSDK())
            return;

        if (!this.loadingIndicator) {
            this.loadingIndicator = document.createElement('div');
            this.loadingIndicator.className = STYLES.LOADING_INDICATOR;
            if (this.wap.config.isVPAID()) {
                this.loadingIndicator.classList.add(STYLES.LOADING_INDICATOR_VPAID);
            }
            this.background.addElement(this.loadingIndicator);
        }
    }

    hideLoadingIndicator() {
        if (this.loadingIndicator) {
            this.background.removeElement(this.loadingIndicator);
            this.loadingIndicator = null;
        }
    }

    showSkipButton(skipOffset) {
        if (this.background.element && !this.skipButton) {
            // create SkipButton
            this.skipButton = new SkipButton(skipOffset);
            this.skipButton.on(SKIP_BUTTON_EVENTS.CLICK, () => {
                this.wap.onSkipAdClick();
            });
            this.skipButton.create(this.background.element);
        }
    }

    hideSkipButton() {
        if (this.skipButton) {
            this.skipButton.remove();
            this.skipButton = null;
        }
    }

    showError(text) {
        this.hideLoadingIndicator();
        this.showMessage(text);
    }

    showMessage(text) {
        if (this.messageDiv) {
            this.hideMessage();
        }
        this.messageDiv = document.createElement('div');
        this.messageDiv.className = 'alfa-bg-message';
        let span = document.createElement('span');
        span.innerHTML = text;
        this.messageDiv.appendChild(span);
        this.background.addElement(this.messageDiv);
    }

    hideMessage() {
        if (this.messageDiv) {
            this.background.removeElement(this.messageDiv);
            this.messageDiv = null;
        }
    }

    showStartButton() {
        if (this.controlBar.timeBar.isSeeking())
            return;

        this.hideRestartButton();
        this.hideEndingScreen();
        this.hideLoadingIndicator();

        if (!this.buttonStart) {
            this.buttonStart = document.createElement('div');
            this.buttonStart.className = 'alfa-button-start';
            this.buttonStart.addEventListener(ua.isMobile ? 'touchend' : 'click', this.onPlayClick.bind(this), false);
            this.background.addElement(this.buttonStart);

            const bgSize = this.background.size;
            if (bgSize.width < 210 || bgSize.height < 210) {
                this.buttonStart.style.width = '72px';
                this.buttonStart.style.height = '72px';
                this.buttonStart.style.marginLeft = '-36px';
            }
        }
    }

    hideStartButton() {
        if (this.buttonStart) {
            this.buttonStart.parentNode.removeChild(this.buttonStart);
            this.buttonStart = null;
        }
    }

    showNonLinear(nonLinearData) {
        if (!this.nonLinear) {
            if (this.background.element) {
                this.nonLinear = new Overlay(nonLinearData);
                this.nonLinear.on(OVERLAY_EVENTS.VIEW, () => {
                    this.wap.onNonLinearView();
                });
                this.nonLinear.on(OVERLAY_EVENTS.CLOSE, () => {
                    this.hideNonLinear();
                    this.wap.onNonLinearClose();
                });
                this.nonLinear.on(OVERLAY_EVENTS.CONTENT_CLICK, (info) => {
                    this.wap.onNonLinearContentClick(info);
                });
                this.nonLinear.on(OVERLAY_EVENTS.VAST_ERROR, (error) => {
                    this.wap.notifyVastError(error);
                });
                this.nonLinear.create(this.background.element);
            }
        } else {
            this.nonLinear.show();
        }
    }

    hideNonLinear() {
        if (this.nonLinear) {
            this.nonLinear.remove();
            this.nonLinear = null;
        }
    }

    createSharingUI() {
        // create only if allowed
        if (this.wap.config.isNoSharingUI()) {
            return;
        }

        this.shareButton.create(this.background.element);
        this.shareButton.on(SHARE_BUTTON_EVENTS.CLICK, () => {
            if (this.player.isEnded()) {
                return;
            }
            this.wap.pause();
            this.wap.shareCurrentVideo();
        });

        this.shareDialog.create(this.background.element);
        this.shareDialog.on(SHARE_DIALOG_EVENTS.DONE, () => {
            this.showShareButton();
        });
    }

    showShareButton() {
        if (this.wap.scheduler.stage.isAd() || this.wap.config.isVPAID() || this.wap.config.isAdOnly())
            return;

        if (this.player.isEnded())
            return;

        if (!this.wap.config.getVideoFile())
            return;

        if (!this.player.error && !this.shareDialog.isVisible()) {
            this.shareButton.show();
        }
    }

    hideShareButton() {
        this.shareButton.hide();
        this.shareDialog.hide();
    }

    showRestartButton(parent) {
        if (this.controlBar.timeBar.isSeeking()) {
            return;
        }

        parent = parent || this.background.element;
        this.hideStartButton();

        if (!this.restartButton) {
            this.restartButton = document.createElement('div');
            this.restartButton.className = 'alfa-button-restart';
            parent.appendChild(this.restartButton);

            this.restartButton.addEventListener('click', this.doRestart.bind(this));
        }
        return this.restartButton;
    }

    hideRestartButton() {
        if (this.restartButton !== void(0)) {
            this.restartButton.parentNode.removeChild(this.restartButton);
            this.restartButton = void(0);
        }
    }

    doRestart() {
        if (!this.wap.isAllowYoutubeWrapper() && this.wap.config.getYoutubeID()) {
            window.location = this.wap.config.getVideoFile();
            return;
        }

        if (!ua.isIOS || ua.isIOSSmall) {
            this.mediaSpan.classList.remove(STYLES.OPACITY_HIDDEN);
        }

        // if there was an video error
        if (this.player.error != null) {
            let curTime = this.player.currentTime;
            let src = this.player.src;
            this.player.src = '';
            this.player.src = src;
            this.player.currentTime = curTime;
        } else {
            this.player.currentTime = 0;
        }

        this.wap.state.finishedMain = false;
        this.wap.mainVideoTime = 0;
        this.videoElementPlay();
        this.showShareButton();
    }

    showEndingScreen() {
        console.log('[alfa] showEndingScreen');
        this.hideStartButton();
        this.hideShareButton();

        if (!ua.isIOS || ua.isIOSSmall) {
            this.mediaSpan.classList.add(STYLES.OPACITY_HIDDEN);
        }
        this.showPoster();
        if (this.endingScreen === void(0)) {
            this.endingScreen = document.createElement('div');
            this.endingScreen.className = 'alfa-ending-screen';

            // if sharing UI is allowed in config
            if (!this.wap.config.isNoSharingUI()) {
                const shareDiv = document.createElement('div');
                shareDiv.className = 'alfa-share-div';
                this.endingScreen.appendChild(shareDiv);

                const sharePanel = new ShareDialog(true);
                sharePanel.create(shareDiv);
                sharePanel.show(this.wap.config.getShareUrl());
            }

            // show restart button
            let restartButton = this.showRestartButton(this.endingScreen);
            restartButton.classList.add('alfa-small-restart');

            const bgSize = this.background.size;
            if (bgSize.width < 320 || bgSize.height < 240) {
                let w = bgSize.width - 20;
                let h = Math.round(w * 3 / 5);
                if (h > bgSize.height - 60) {
                    h = bgSize.height - 50;
                    w = Math.round(h * 5 / 3);
                }
                this.endingScreen.style.width = w + 'px';
                this.endingScreen.style.height = h + 'px';
            }
            this.background.addElement(this.endingScreen);
        }

        this.controlBar.timeBar.setPlayPosition(100);
    }

    hideEndingScreen() {
        if (this.endingScreen !== void(0)) {
            this.endingScreen.parentNode.removeChild(this.endingScreen);
            this.endingScreen = void(0);
        }
    }

    showIcons() {
        // container element for VAST icons
        this.iconsContainer.create(this.background.element);
        this.iconsContainer.on(ICON_EVENTS.ICON_VIEW, (icon) => {
            this.wap.onIconView(icon);
        });
        this.iconsContainer.on(ICON_EVENTS.ICON_CLICK, (icon) => {
            this.wap.onIconClick(icon);
        });
    }

    hideIcons() {
        this.iconsContainer.off(ICON_EVENTS.ICON_VIEW);
        this.iconsContainer.off(ICON_EVENTS.ICON_CLICK);
        this.iconsContainer.remove();
    }

    resetTimeAndSeek() {
        this.controlBar.totalTime.setTime(0);
        this.controlBar.timeBar.setTime(0);
        this.controlBar.timeBar.setPosition(0);
    }

    videoElementPlay() {
        this.hideMessage();
        this.hideEndingScreen();
        this.hideRestartButton();
        this.hideEndingScreen();

        if (this.wap.config.isVPAID()) {
            this.controlBar.showPauseButton();
        }

        // run 'play' when video player is ready
        playWhenCan(this.player);
    }

    disablePlay() {
        this.controlBar.off(CONTROLS_BAR_EVENTS.PLAY_PAUSE_CLICK);
    }

    calculateBufferWidth() {
        if (this.wap.config.isVPAID())
            return;

        let len = this.player.buffered.length;

        if (len > 0) {
            let lastSec = this.player.buffered.end(len - 1);
            let duration = this.player.duration;
            let percent = (lastSec / duration * 100);
            if (percent > this.controlBar.timeBar.getBufferedPosition()) {
                this.controlBar.timeBar.setBufferedPosition(percent);
            }
        } else {
            this.controlBar.timeBar.setBufferedPosition(0);
        }
    }

    setVolume(vol) {
        this._volume = vol;

        // TODO: why return?
        if (ua.isIOS || ua.isUCBrowser) {
            return;
        }

        // toggle MUTED attribute on element with according to volume level
        this.player.mute(vol === 0);

        if (this.player.volume > vol && vol === 0) {
            this.wap.onMute();
        } else if (this.player.volume === 0 && vol > 0) {
            this.wap.onUnmute();
        }

        // maintain same volume in all available players
        if (this.videoPlayer) {
            this.videoPlayer.volume = vol;
        }
        if (this.audioPlayer) {
            this.audioPlayer.volume = vol;
        }
        if (this.youtubePlayer) {
            this.youtubePlayer.volume = vol;
        }
        if (this.vpaidPlayer) {
            this.vpaidPlayer.volume = vol;
        }

        this.wap.emit(MEDIA_EVENTS.volumechange, vol);
        this.setVolumeUI(vol);
    }

    setVolumeUI(vol) {
        this._volume = vol;
        this.buttonMute.muted(vol === 0);
        this.controlBar.updateVolume(vol);
    }

    getVolume() {
        return this.player.volume;
    }

    createPoster() {
        if (this.videoPoster) {
            return;
        }

        const posterUrl = this.wap.config.getPosterUrl();
        if (posterUrl) {
            this.videoPoster = document.createElement('div');
            this.videoPoster.className = 'alfa-video-poster';
            this.videoPoster.style.backgroundImage = 'url(' + posterUrl + ')';
            this.background.addElement(this.videoPoster);
        }
    }

    showPoster() {
        if (this.videoPoster) {
            this.videoPoster.style.visibility = 'visible';
        }
    }

    hidePoster() {
        if (this.videoPoster) {
            // this.background.removeElement(this.videoPoster);
            // this.videoPoster = null;
            this.videoPoster.style.visibility = 'hidden';
        }
    }

    isFullscreen() {
        return fscreen.fullscreenElement || this.background.isFullscreen();
    }

    enterFullscreen() {
        if (fscreen.fullscreenEnabled) {
            fscreen.requestFullscreen(ua.isIOS ? this.player : this.background.element);
        } else {
            this.background.enterFullscreen();
        }

        this.wap.onEnterFullscreen();
    }

    exitFullscreen() {
        if (fscreen.fullscreenEnabled) {
            fscreen.exitFullscreen();
        } else {
            this.background.exitFullscreen();
        }

        this.wap.onExitFullscreen();
    }

    onFullscreenChange() {
        if (this.background.isFullscreen()) {
            this.background.exitFullscreen();
        }

        if (this.vpaidPlayer)
            this.vpaidPlayer.onParentResize();
    }

    onFullscreenClick() {
        this.setupHideControlsTimer();

        if (!this.isFullscreen()) {
            this.enterFullscreen();
        } else {
            this.exitFullscreen();
        }
    }

    viewabilityPause() {
        if (this.wap.scheduler.stage.isAd() && !this.wap.isPaused()) {
            this.__wasPausedByViewability = true;
            this.wap.pause();
        }
    }

    viewabilityPlay() {
        if (this.__wasPausedByViewability && this.wap.scheduler.stage.isAd() && this.wap.isPaused()) {
            this.__wasPausedByViewability = false;

            if (!this.player.isEnded() && !this.wap.state.initial) {
                this.wap.play();
            }
        }
    }

    get isSeekingNow() {
        return this.controlBar.timeBar.isSeeking();
    }
}

// ************************************** Events ***********************************************
UIManager.prototype.onContextMenuRequested = function (e) {
    // if menu requested on our UI
    if (!this.background.isInBounds(e.toElement)) {
        return;
    }
    e.preventDefault();

    const items = [
        {
            id: 'about_player',
            text: 'About Web Alfa player',
            href: 'http://www.alfaplayer.com/' + (this.wap.config.isVPAID() ? 'share/' : '')
        }
    ];
    showContextMenuAt(e.x, e.y, items, this.background.element);
};

UIManager.prototype.onCheckCurrentPlayer = function () {
    const id = settings.currentPlay || this.randomId;
    if (id !== this.randomId && this.player && !this.player.paused) {
        this.player.pause();
    }
    setTimeout(this.onCheckCurrentPlayer.bind(this), CHECK_PLAYER_TIMEOUT);
};

UIManager.prototype.onBuffering = function () {
    console.log('[alfa] OnBuffering');
    this.hidePoster();
    this.controlBar.showPauseButton();

    this.mediaSpan.classList.remove(STYLES.OPACITY_HIDDEN);
};

UIManager.prototype.onWindowMessage = function (e) {
    let data = e.data + '';
    if (data.indexOf('ALFAPLAYER_PLAY_EVENT') === 0) {
        if (parseFloat(data.split(':')[1]) !== this.randomId) {
            this.wap.pause();
        }
    }
};

UIManager.prototype.onVolumeClick = function () {
    console.log('[alfa] on volume button click');

    if (this._volume > 0) {
        // mute
        this._previousVolume = this._volume;
        settings.volume = 0;
        this.setVolume(0);
    } else if (this._previousVolume > 0) {
        // unmute
        settings.volume = this._previousVolume;
        this.setVolume(this._previousVolume);
    } else {
        settings.volume = 1;
        this.setVolume(1);
    }
};

UIManager.prototype.onVolumeChange = function (percent) {
    this.setupHideControlsTimer();

    settings.volume = percent;  // save volume to localStorage
    this.setVolume(percent);    // change volume in video element
};

UIManager.prototype.onPlayClick = function (e) {
    e.stopPropagation();
    e.preventDefault();

    if (!this.wap.config.isVPAID()) {
        settings.currentPlay = this.randomId;
    }

    this.wap.play();
};

UIManager.prototype.onPlayPauseClick = function () {
    this.setupHideControlsTimer();

    if (this.wap.isPaused()) {
        if (!this.wap.config.isVPAID()) {
            settings.currentPlay = this.randomId;
        }

        if (!this.wap.scheduler.stage.isAd() && this.player.isEnded() && !this.wap.state.initial) {
            this.doRestart();
        } else {
            this.wap.play();
        }
    } else {
        this.wap.pause();
    }
};

UIManager.prototype.onPause = function () {
    if (this.controlBar.timeBar.isSeeking())
        return;

    this.controlBar.showPlayButton();
    this.showControlBar();
    this.hideLoadingIndicator();

    if (this.wap.scheduler.stage.isBegin())
        return;

    if (!this.wap.scheduler.stage.isAd()) {
        if (this.player.error !== null)
            this.showRestartButton();
        else if (!this.player.isEnded() && !this.wap.state.finishedMain) {
            this.showStartButton();
            console.log('[alfa] Show start button on pause', this.player.currentTime, this.player.duration);
        }
    } else if (!this.player.isEnded()) {
        this.showStartButton();
    }

    this.wap.onPause();
};

UIManager.prototype.onPlay = function () {
    this.hideStartButton();
    this.hideEndingScreen();
    this.hideRestartButton();
    this.hidePoster();
    this.controlBar.showPauseButton();
    this.wap.onPlay();
    let mediaSpan = this.mediaSpan;
    if (mediaSpan) {
        this.mediaSpan.classList.remove(STYLES.OPACITY_HIDDEN);
    }

    if (!this.wap.config.isVPAID()) {
        settings.currentPlay = this.randomId;

        let sendplayevent = function (wnd) {
            wnd.postMessage('ALFAPLAYER_PLAY_EVENT:' + this.randomId, '*');
        }.bind(this);
        sendplayevent(window.self);
    }
};

UIManager.prototype.onDurationChange = function () {
    this.controlBar.totalTime.setTime(this.player.duration);
    this.wap.emit(MEDIA_EVENTS.durationchange, this.player.duration);
};

UIManager.prototype.onLoadstartEvent = function () {
    this.controlBar.totalTime.setTime(0);
    if (!this.controlBar.isAdsLayout()) {
        this.controlBar.timeBar.setTime(0);
    }
    // this.controlBar.timeBar.setPosition(0);
};

UIManager.prototype.onTimeUpdate = function () {
    const duration = this.player.duration;
    const curTime = this.player.currentTime;

    if (ua.isIE) {
        this.hideLoadingIndicator();
    }

    // update SkipButton text on video time progress (if exists)
    if (this.skipButton) {
        this.skipButton.update(curTime);
    }

    if (this.controlBar.isAdsLayout()) {
        // show ad icons for current time, if any
        if (this.wap.currentAd && this.wap.currentAd.hasIcons()) {
            // TODO: listen to resize of root player container, instead of resizing every second
            this.iconsContainer.resizeToVideo(this.player.element);

            const icons = this.wap.currentAd.getIconsForTime(curTime);
            this.iconsContainer.showIcons(icons);
        }

        if (duration >= curTime) {
            const remain = duration - curTime;
            if (remain > 0)
                this.controlBar.currentTime.setText('Ad remains:', remain);
            else
                this.controlBar.currentTime.setText('');
        } else {
            this.controlBar.currentTime.setText('');
        }
    } else {
        this.controlBar.timeBar.setTime(curTime);

        const percent = curTime / duration * 100;
        this.controlBar.timeBar.setPlayPosition(percent);
    }
};

UIManager.prototype.onProgress = function () {
    this.calculateBufferWidth();
};

UIManager.prototype.onCanPlaythroughEvent = function () {
    this.calculateBufferWidth();
};

UIManager.prototype.onMouseOverPlayer = function () {
    if (!ua.isMobile) {
        this.showControlBar();
    }
};

UIManager.prototype.onMouseOutOfPlayer = function () {
    if (!ua.isMobile && !this.player.paused && this.player.hasDuration()) {
        this.setupHideControlsTimer(300);
    }

    if (this.shareDialog.isVisible()) {
        this.shareDialog.hide();
        if (!this.wap.state.finishedMain)
            this.showShareButton();
    }
};

UIManager.prototype.onTimeBarSeekStart = function () {
    if (!this.player) {
        return;
    }

    this.wasPlayingBeforeSeek = this.player.paused === false;
};

UIManager.prototype.onTimeBarSeek = function (percent) {
    if (this.player.hasDuration()) {
        if (ua.isMobile) {
            this.setupHideControlsTimer();
        }

        // rewind current player
        // HINT: rewinding not working if "Disable Cache" is set in browser DevTools
        const newTime = this.player.duration * percent / 100;
        this.player.currentTime = newTime;
    }
};

// TODO: refactor
UIManager.prototype.onTimeBarSeekEnd = function () {
    if (this.wasPlayingBeforeSeek && !this.player.isEnded())
        this.videoElementPlay();

    if (this.player.isEnded()) {
        this.onPlayFinished();
        this.onPause();
    } else if (!this.wasPlayingBeforeSeek) {
        this.showStartButton();
    }
};



