import { VAST_EVENTS, PLAYER_EVENTS, MEDIA_EVENTS, MEDIA_ERROR } from '@/data/constants';
import { parsedUA as ua } from '@/data/userAgent';
import { createError, createVastError, sendError, VastError, VAST_ERRORS } from '@/system/errors';
import { getShortURL } from '@/system/networking';
import Scheduler from '@/system/scheduler';
import EventSystem from '@/system/eventSystem';
import { CompanionArea, EVENTS as COMPANION_EVENTS } from '@/ui/companionArea';
import { STYLES } from '@/ui/styles';
import { getUrlContentAsync, playWhenCan } from '@/utils/common';
import { prepareFetchVastUrl } from '@/utils/url';
import { STAGES } from '@/utils/stage';

import Config from '@/data/config';
import UIManager from '@/ui/uiManager';
import { settings } from '@/data/settings';
import { VastNotifier } from '@/system/vastNotifier';
import { parse } from './parser';
import { notifyInfo, notifyCreate } from '@/system/notifier';

const DEFAULT_PUI = '35vz1fd5a348b2bf6841a0a4b8g3e281',
    VAST_CLOSE_AFTER_SHOW_MIDROLL = true,
    VAST_CLOSE_AFTER_SHOW_ONLOAD = true;

class WebAlfaPlayer extends EventSystem {
    constructor(pui) {
        super();

        this.config = new Config(pui || DEFAULT_PUI);
        this.vast = null;
        this.state = {
            // Just loaded, haven't played anything
            initial: true,
            // is main video file playing finished
            finishedMain: false
        };
        // session ID for this player instance
        this.sid = settings.nextSID();

        this.currentAd = null;
        this.currentAdNumber = 0;
        this.currentCompanion = null;
        this.mainVideoTime = 0;

        this.notifier = new VastNotifier();
        this.ui = new UIManager(this);
        this.scheduler = new Scheduler(this);
        this.thisTimeUpdateEvent = this.onTimeUpdate.bind(this);
    }

    notifyOnce(eventName) {
        const fired = this.notifier.fireLinearOnce(eventName);
        if (fired) {
            this.emit(PLAYER_EVENTS.AD_TRACKING, eventName);
        }
    }

    notifyImpressions() {
        const fired = this.notifier.fireImpressions();
        if (fired) {
            this.emit(PLAYER_EVENTS.AD_IMPRESSION);

            // in debug build do info report about impressions firing
            if (process.env.ALFA_DEBUG) {
                this.notifyInfo('impressions');
            }
        }
    }

    create(config) {
        this.state.initial = true;

        // reading player config
        this.config.init(config);

        // creating of UI and video elements
        this.ui.createMainUI(this.config);

        // we should notify server about successful player creation
        this.notifyCreate();

        // setup debug 'unload' event reporting
        if (process.env.ALFA_DEBUG) {
            window.addEventListener('unload', () => this.notifyInfo('unload'));
        }

        // if ads will be requested from server by PUI
        if (this.config.isAutoGetAds()) {
            this.playCurrentAd();
        } else {
            // try to load VAST data from provided XML or URL
            this.loadVast(() => this.onConfigApplied());
        }
    }

    /**
     * notify server about successful player creation
     */
    notifyCreate() {
        // additional data
        const data = {};

        // additional data from config
        this.config.fillNotifyProps(data);

        // report initial viewability
        if (this.ui.viewability) {
            data.viewability = this.ui.viewability.getViewability();
        }

        // make request
        notifyCreate({
            sid: this.sid,
            data
        });
    }

    notifyInfo(name) {
        // additional data
        const data = {};

        // additional data from config
        this.config.fillNotifyProps(data);

        // additional data about current ad
        this.fillCurrentAdProps(data);

        // report initial viewability
        if (this.ui.viewability) {
            data.viewability = this.ui.viewability.getViewability();
        }

        // make request
        notifyInfo(name, {
            sid: this.sid,
            data
        });
    }

    notifyError(error) {
        // add player instance ID
        error.sid = this.sid;

        // additional error data
        const data = {};
        // additional data from config
        this.config.fillNotifyProps(data);

        // additional data about current ad
        this.fillCurrentAdProps(data);

        // in DEBUG build we report VAST content in case of VastError
        if (process.env.ALFA_DEBUG && (error instanceof VastError)) {
            data.debug = error.source || (this.vast && this.vast.source) || '';
        }

        // notify our server about error
        sendError(error, data);
    }

    notifyVastError(error) {
        // notify VAST provider about VAST error
        if (error.code > 0) {
            this.notifier.fireError(error.code);
        }

        // notify our server about error
        this.notifyError(error);
    }

    fillCurrentAdProps(data) {
        // if we already selected AD
        if (this.currentAd) {
            // vast type
            data.vast = this.currentAd.getVastType();

            // if we determined mediaFile
            if (this.currentAd.mediaFile) {
                data.mediafile = this.currentAd.mediaFile;
            }
        }
    }

    loadVast(callback) {
        if (this.config.getFetchVastUrl()) {
            // TODO: maybe we can put 'prepareFetchVastUrl' inside of 'getFetchVastUrl'?
            // prepare URL to fetch VAST file from server
            let vastUrl = this.config.getFetchVastUrl();
            if (this.config.isVPAID() && this.config.getVideoElement() && this.config.getVideoElement().getBoundingClientRect) {
                vastUrl = prepareFetchVastUrl(vastUrl, this.config.getVideoElement().getBoundingClientRect());
            }

            console.log('[alfa] Fetching VAST from url: ' + vastUrl);
            getUrlContentAsync(vastUrl, (resultXML) => {
                if (!resultXML) {
                    return callback();
                }

                this.initVast(resultXML, callback);
            });
        } else if (this.config.getProvidedVastXML()) {
            this.initVast(this.config.getProvidedVastXML(), callback);
        } else {
            callback();
        }
    }

    initVast(vastXML, callback) {
        // listen to 'warning' events that may appear while parsing VAST xml
        parse(vastXML,
            (warning) => {
                this.notifyVastError(warning);
            },
            (error, data) => {
                // if critical error occurred while parsing
                if (error) {
                    this.notifyVastError(error);

                    this.vast = null;
                } else {
                    this.vast = data;
                }

                callback();
            });
    }

    onConfigApplied() {
        if (ua.isMobile) {
            this.scheduler.precacheAd();
        }

        // setup OnClick listener on video element, if it was provided by config
        const videoElement = this.config.getVideoElement();
        if (videoElement) {
            videoElement.addEventListener(ua.isMobile ? 'touchend' : 'click', this.onPlayerClick.bind(this));
        }
        // setup OnClick listener on player root element
        this.ui.background.on(PLAYER_EVENTS.CLICK, this.onPlayerClick.bind(this));

        if (this.config.getYoutubeID() && this.ui.youtubePlayer) {
            this.ui.youtubePlayer.on(MEDIA_EVENTS.timeupdate, this.thisTimeUpdateEvent);
        }
        this.ui.player.on(MEDIA_EVENTS.timeupdate, this.thisTimeUpdateEvent);

        if (this.haveAdTags()) {
            this.setCurrentAd(0);
        }

        // if video is not marked as adult, we can run autoplay, or setup autoplay on touch
        if (this.config.isAutoplay() && !this.config.isAdult()) {
            if (this.config.isAndroidSDK() || !ua.isMobile) {
                this.play();
            } else if (ua.isMobile) {
                // if video is muted - try to play it automatically, it's allowed on devices with iOS >= 10 and android+chrome >= 53
                if (this.config.isMute()) {
                    this.play();
                }

                // setup autoplay on any touch event on page (even if we started muted video)
                this.setupTouchAutoplay();
            }
        }

        this.emit(PLAYER_EVENTS.PLAYER_CREATED);
    }

    /**
     * Setup TOUCHEND handler on document and parent document, if player is in IFRAME.
     * Handler will be capturing, so we can disable bubbling in case touch was in player borders.
     */
    setupTouchAutoplay() {
        const touchAutoplay = (e) => {
            // prevent from firing TOUCHEND handler on backgroundElement, if this touch was in its borders
            if (this.ui.background.isInBounds(e.target)) {
                e.stopPropagation();
            }

            // if we already playing video - simply remove handlers
            if (!this.state.initial) {
                document.body.removeEventListener('touchend', touchAutoplay, true);
                try {
                    if (window.parent && window.parent.document) {
                        window.parent.document.removeEventListener('touchend', touchAutoplay, true);
                    }
                } catch (e) {
                    // catch all
                }
            }

            if (this.scheduler.isGettingAd()) {
                return;
            }

            // start playing video only of player is in browser viewport
            if (this.ui.isInViewport()) {
                document.body.removeEventListener('touchend', touchAutoplay, true);
                if (this.state.initial) {
                    this.play();
                }
            }
        };

        console.log('[alfa] setup video_autoplay on touchend');
        document.body.addEventListener('touchend', touchAutoplay, true);
        try {
            if (window.parent && window.parent.document) {
                window.parent.document.addEventListener('touchend', touchAutoplay, true);
            }
        } catch (e) {
            console.log('[alfa] Error:', e.toString());
        }
    }

    getAdTags() {
        if (!this.vast) {
            return [];
        }
        return this.vast.adTags || [];
    }

    haveAdTags() {
        return this.getAdTags().length > 0;
    }

    setCurrentAd(number) {
        this.iosfwdlimit = void(0);
        this.currentAd = this.getAdTags()[number];
        this.currentAdNumber = number;
        this.notifier.init(this.currentAd);
        this.ui.hideNonLinear();

        if (this.config.getCompanionConfig() && this.currentAd.companions.length > 0) {
            // remove current visible companion
            if (this.currentCompanion) {
                this.currentCompanion.remove();
            }

            this.currentCompanion = CompanionArea.create(this.config.getCompanionConfig(), this.currentAd.companions);
            if (this.currentCompanion) {
                this.notifier.fireByUrl(this.currentCompanion.companionInfo.events[VAST_EVENTS.CREATIVE_VIEW]);
                // set up VAST error notifications
                this.currentCompanion.on(COMPANION_EVENTS.VAST_ERROR, (error) => this.notifyVastError(error));
            } else {
                const error = createVastError(VAST_ERRORS.COMPANION_SUPPORT, 'Not found supported STATIC Companion');
                this.notifyVastError(error);
            }
        }
    }

    play() {
        console.log('[alfa] Play');
        this.ui.hideStartButton();
        this.ui.showShareButton();

        // setup timer to check if autoplay was successfull
        if (this.state.initial && this.config.isAutoplay()) {
            this.scheduler.watchForAutoplay();
        }

        if (this.scheduler.willPlayAdASAP()) {
            return;
        }
        if (!this.ui.mediaSpan) {
            console.error('Cannot start playing. Player is not created yet.');
            return;
        }

        this.ui.mediaSpan.classList.remove(STYLES.OPACITY_HIDDEN);

        this.state.finishedMain = false;
        if (this.state.initial && (this.config.isAutoGetAds() || settings.isRotated)) {
            this.scheduler.onPlay();
        } else {
            if (this.state.initial) {
                this.scheduler.stage.next(STAGES.PRE_ROLL);
                this.playCurrentAd();
            } else {
                this.ui.videoElementPlay();
                this.emit(MEDIA_EVENTS.play);
            }
        }
        this.ui.showControlBar();
        this.state.initial = false;
    }

    pause() {
        console.log('[alfa] Pause');
        if (this.ui.player && !this.ui.player.paused) {
            this.ui.player.pause();
            this.ui.showStartButton();
        }
    }

    resume() {
        console.log('[alfa] Resume');
        if (this.ui.player && this.ui.player.paused) {
            this.ui.player.play();
            this.ui.hideMessage();
        }
    }

    abortPlaying() {
        this.ui.player.pause();
        this.ui.player.src = '';
    }

    isPaused() {
        return this.ui.player.paused || this.state.initial;
    }

    playVPAIDAd(mediaFile) {
        if (this.ui.player && !this.ui.player.paused) {
            this.ui.player.pause();
        }
        if (ua.isMobile) {
            this.ui.showControlBar();
        }

        console.log('[alfa] VPAID ad start play');

        this.ui.showVPAIDPlayer();
        this.ui.vpaidPlayer.type = mediaFile.type;
        this.ui.vpaidPlayer.src = mediaFile.url;
        this.ui.hideShareButton();
        this.ui.morphToAdsLayout();
        //this.ui.vpaidPlayer.play();
    }

    playCurrentAd(videoStage) {
        this.scheduler.preCachedAd = false;

        if (!videoStage) {
            videoStage = (this.ui.player.currentTime < 1) ? STAGES.PRE_ROLL : STAGES.MID_ROLL;
        }

        console.log('[alfa] playCurrentAd STAGE=' + videoStage);

        // TODO: maybe select mediafile here, instead of in parser
        const linear = this.currentAd ? this.currentAd.linear : null;
        const mediaFile = this.currentAd ? this.currentAd.mediaFile : null;

        // if we couldn't find mediaFile, then just skip to main video
        if (!mediaFile || !mediaFile.url) {
            this.ui.showVideoPlayer();

            this.emit(PLAYER_EVENTS.AD_FINISHED);
            if (!this.config.isAdOnly()) {
                this.playMainVideo();
            } else {
                this.ui.player.removeEventListener(MEDIA_EVENTS.timeupdate, this.thisTimeUpdateEvent);
                this.scheduler.stage.next(STAGES.BEGIN);
            }
            return;
        }

        this.scheduler.stage.next(videoStage || STAGES.MID_ROLL);

        // if current ad tag is VPAID then show VPAID player, else show HTML5 player
        if (mediaFile && mediaFile.isVPAID()) {
            this.playVPAIDAd(mediaFile);
        } else {
            this.playHTML5Ad(linear, mediaFile);
        }
    }

    playHTML5Ad(linear, mediaFile) {
        this.ui.showVideoPlayer();
        try {
            if (this.ui.player.src !== mediaFile.url) {
                this.ui.player.src = mediaFile.url;
            }

            this.ui.hideShareButton();
            this.ui.morphToAdsLayout();
            this.ui.showIcons();
            if (ua.isMobile)
                this.ui.showControlBar();

            if (!this.scheduler.stage.isPostRoll()) {
                if (!this.config.isAdOnly() || this.config.isVPAID()) {
                    // determine ad skip offset and show button
                    const offset = linear.skipOffset || this.config.getSkipOffset() || 0;
                    this.ui.showSkipButton(offset);
                }
            }

            this.ui.videoElementPlay();

            this.notifyOnce(VAST_EVENTS.START);
            this.notifier.fireLinear(VAST_EVENTS.CREATIVE_VIEW);

            if (this.ui.player.paused && ua.isMobile) {
                console.warn('[alfa] Mobile play function fail, show start button');
                this.ui.showStartButton();
            }

            if (!ua.isMobile) {
                if (this.ui.getVolume() === 0) {
                    this.notifier.fireLinear(VAST_EVENTS.MUTE);
                }
            } else if (this.config.isMute()) {
                this.ui.setVolume(0);
            }
        } catch (e) {
            console.log('[alfa] Play Ad exception,', e);
            this.notifyError(createError(e));
            this.playMainVideo();
        }
    }

    playMainVideo() {
        const src = this.config.getYoutubeID() || this.config.getVideoFile();
        console.log(`[alfa] Play Main video: '${ src }'`);

        // if we already playing this video - just try to unpause it
        if (this.scheduler.stage.isVideo() && this.ui.player.src === src) {
            if (this.ui.player.paused) {
                playWhenCan(this.ui.player);
            }

            return;
        }

        this.ui.showVideoPlayer();
        this.ui.hideCloseButton();
        this.ui.hideSkipButton();
        this.ui.hideIcons();

        if (this.config.getVideoFile()) {
            this.ui.morphToNormalLayout();
            this.state.finishedMain = false;

            this.scheduler.stage.next(STAGES.MAIN_VIDEO);
            if (this.config.getYoutubeID()) {
                if (!this.isAllowYoutubeWrapper() && this.config.getVideoFile()) {
                    this.state.finishedMain = true;
                    this.scheduler.stage.next(STAGES.END);
                    this.ui.showEndingScreen();
                    this.ui.resetTimeAndSeek();
                    window.location = this.config.getVideoFile();
                    return;
                }

                console.log('[alfa] Show youtube player');
                this.ui.showYoutubePlayer();

                if (ua.isAndroid && ua.isChrome) {
                    setTimeout(() => {
                        if (this.ui.youtubePlayer.state !== 1) {
                            this.ui.onPause();
                        }
                    }, 700);
                }
            }

            if (this.ui.player.src !== src) {
                this.ui.showLoadingIndicator();
                this.ui.player.src = src;
            }

            // show NonLinear if present and supported
            if (this.currentAd) {
                const nonLinear = this.currentAd.getSupportedNonLinear();
                if (nonLinear) {
                    this.ui.showNonLinear(nonLinear);
                } else if (this.currentAd.nonLinears.length > 0) {
                    this.notifyVastError(createVastError(VAST_ERRORS.NONLINEAR_SUPPORT, 'Couldnt find NonLinear resource with supported type.'));
                }
            }

            // set full volume in player on mobile devices, so volume can be regulated by hardware buttons
            if (this.config.isMute() && ua.isMobile && !this.config.isAutoplay()) {
                this.ui.setVolume(1);
            }

            try {
                playWhenCan(this.ui.player,
                    () => {
                        console.log('[alfa] Playing main video');
                    },
                    (error) => {
                        if (this.ui.player.src !== src) {
                            console.log('[alfa] Error initiating main video playback, invalid SRC: ', error);
                            this.playMainVideo();
                        } else if (this.ui.player.paused) {
                            console.log('[alfa] Error initiating main video playback: ', error);
                            this.ui.showStartButton();
                        }
                    }
                );
                this.ui.showShareButton();
            } catch (error) {
                console.log('[alfa] Error initiating main video playback: ', error);
                this.ui.showStartButton();
            }

            if (this.mainVideoTime) {
                this.ui.seekToTime(this.mainVideoTime);
            }
        } else {
            console.log('[alfa] No main video file');
            this.scheduler.stage.next(STAGES.BEGIN);
            this.ui.hideLoadingIndicator();
            this.ui.player.pause();
            this.ui.hideShareButton();
            //this.ui.currentTime.innerHTML = '';
            this.ui.mediaSpan.classList.add(STYLES.OPACITY_HIDDEN);
            this.ui.showMessage('No video');
            this.ui.disablePlay();
        }
    }

    showWaitingForAd() {
        console.log('[alfa] showWaitingForAd');
        this.ui.morphToWaitingAdsLayout();
    }

    hideWaitingForAd() {
        this.ui.hideLoadingIndicator();
        this.ui.morphToNormalLayout();
    }

    getPlayerSize() {
        return this.ui.background.size;
    }

    shareCurrentVideo() {
        console.log('[alfa] share video');
        let shareUrl;

        // TODO: check for real domain
        // if current video is shared from our tinyurl page that use our PUI
        if (document.URL.indexOf('OURTINYURL') >= 0) {
            shareUrl = this.config.getShareUrl(DEFAULT_PUI);
        } else {
            shareUrl = this.config.getShareUrl();
        }

        this.ui.showShareDialog(shareUrl);
    }

    getShareLink(callback) {
        let queries = this.config.getShareUrl();
        getShortURL(queries, callback);
    }

    showAd() {
        window.open(this.config.getShowAdUrl(), '_blank');
    }

    closePlayer() {
        this.ui.background.destroy();
    }

    isAllowYoutubeWrapper() {
        return !ua.isMobile
            || ua.isFirefox
            || (ua.isAndroid && ua.isChrome && !ua.isFacebook);
    }

    get currentTime() {
        return this.ui.player.currentTime;
    }

    set currentTime(value) {
        if (!this.scheduler.stage.isAd())
            this.ui.player.currentTime = value;
    }

    onNonLinearView() {
        this.notifier.fireNonLinear(VAST_EVENTS.CREATIVE_VIEW);
    }

    onNonLinearContentClick(nonLinear) {
        this.pause();
        if (nonLinear.clickTracking) {
            this.notifier.fireByUrl(nonLinear.clickTracking);
        }
    }

    onNonLinearClose() {
        this.notifier.fireNonLinear(VAST_EVENTS.CLOSE);
    }

    onIconView(icon) {
        if (icon && icon.viewTracking) {
            this.notifier.fireByUrl(icon.viewTracking);
        }
    }

    onIconClick(icon) {
        if (!icon) {
            return;
        }

        if (icon.clickTracking) {
            this.notifier.fireByUrl(icon.clickTracking);
        }
        if (icon.clickThrough) {
            this.pause();
            window.open(icon.clickThrough, '_blank');
        }
    }
}

// @Events handlers
WebAlfaPlayer.prototype.onTimeUpdate = function () {
    let currentTime = this.ui.player.currentTime;
    let duration = this.ui.player.duration;

    let time = +new Date();
    if (!this.prevTimeNotify || (time - this.prevTimeNotify > 999)) {
        this.prevTimeNotify = time;
        this.emit(MEDIA_EVENTS.timeupdate, currentTime, duration);
    }

    // notify scheduler about current playing time
    this.scheduler.onTimeUpdate(currentTime, duration);

    if (this.scheduler.stage.isVideo()) {
        this.mainVideoTime = currentTime;
    } else if (this.scheduler.stage.isAd()) {
        if (currentTime > 0) {
            this.notifyImpressions();
        }

        // check if we need to fire 'progress' tracking event
        if (this.currentAd.hasProgressEvents()) {
            this.notifier.fireProgress(currentTime);
        }

        if (ua.isIOSSmall) {
            if (this.previousTime !== void(0) && ((currentTime - this.previousTime) > (Math.log(duration * duration + duration * 2 + 10) / 5))) {
                console.log('[alfa] fwd seek catched');
                this.iosfwdlimit = currentTime / duration;
                this.previousTime = currentTime;
                return;
            } else if (this.previousTime > currentTime) { //seek backward

            }

            this.previousTime = currentTime;
            if (currentTime > 0.75 * duration) {
                if ((this.iosfwdlimit !== void(0) ? this.iosfwdlimit : 0) < 0.75)
                    if (currentTime < duration)
                        this.notifyOnce(VAST_EVENTS.THIRD_QUARTILE);
            } else if (currentTime > 0.50 * duration) {
                if ((this.iosfwdlimit !== void(0) ? this.iosfwdlimit : 0) < 0.5)
                    this.notifyOnce(VAST_EVENTS.MIDPOINT);
            } else if (currentTime > 0.25 * duration) {
                if ((this.iosfwdlimit !== void(0) ? this.iosfwdlimit : 0) < 0.25)
                    this.notifyOnce(VAST_EVENTS.FIRST_QUARTILE);
            }
        } else {
            if (currentTime > 0.75 * duration) {
                if (currentTime < duration)
                    this.notifyOnce(VAST_EVENTS.THIRD_QUARTILE);
            } else if (currentTime > 0.50 * duration) {
                this.notifyOnce(VAST_EVENTS.MIDPOINT);
            } else if (currentTime > 0.25 * duration) {
                this.notifyOnce(VAST_EVENTS.FIRST_QUARTILE);
            }
        }
    }
};

WebAlfaPlayer.prototype.onPause = function () {
    if (this.scheduler.stage.isAd() && !this.ui.player.isEnded()) {
        this.notifier.fireLinear(VAST_EVENTS.PAUSE);
    }
};

WebAlfaPlayer.prototype.onPlay = function () {
    this.ui.hideMessage();
    this.ui.hideLoadingIndicator();
    if (this.scheduler.stage.isAd()) {
        this.ui.hideShareButton();
        if (this.ui.player.currentTime > 0) {
            this.notifier.fireLinear(VAST_EVENTS.RESUME);
        }
    }
};

WebAlfaPlayer.prototype.onPlayerClick = function (e) {
    //e.preventDefault(); // DO NOT TOUCH! iOS bug
    if (this.__playerClickTimeStamp !== 0 && this.__playerClickTimeStamp === e.timeStamp)
        return;
    this.__playerClickTimeStamp = e.timeStamp;

    // don't do anything on initial stage
    if (this.scheduler.stage.isBegin()) {
        return;
    }

    // catch clicks only on eligible elements
    if (!this.ui.isValidForClickThrough(e.target)) {
        return;
    }

    console.log('[alfa] Click on ClickThrough element');
    if (this.scheduler.stage.isAd()) {
        if (this.isPaused() && !this.config.isAdOnly()) {
            this.play();
        } else {
            this.onClickThrough(this.currentAd.videoClickThrough, '', true);
        }
    } else {
        this.onPlayerClickMain();
    }
};

WebAlfaPlayer.prototype.onClickThrough = function (url, id, playerHandles) {
    url = (url || this.currentAd.videoClickThrough);
    if (url) {
        this.notifier.fireByUrl(this.currentAd.getVideoClickTrackings());
        this.pause();

        // if player in ROTATED mode, then resume playing after 10 seconds (MWA-554 + MWA-556)
        if (settings.isRotated) {
            setTimeout(() => {
                if (this.isPaused()) {
                    this.resume();
                }
            }, 10000);
        }
    }

    if (!this.config.isVPAID() && !this.config.isAndroidSDK() && playerHandles && url) {
        window.open(url, '_blank');
    }

    this.emit(PLAYER_EVENTS.AD_CLICK_THROUGH, url, id, playerHandles);
};

WebAlfaPlayer.prototype.onPlayerClickMain = function () {
    this.ui.showControlBar();

    if (this.ui.isControlBarVisible() || this.config.isNoUI()) {
        if (!this.state.finishedMain && !this.ui.player.error) {
            if (this.isPaused())
                this.play();
            else
                this.pause();
        }
    }
};

WebAlfaPlayer.prototype.onPlayFinished = function () {
    console.log('[alfa] play finished "' + this.scheduler.stage.current + '"');
    if (this.ui.isSeekingNow)
        return;

    if (this.scheduler.stage.isAd()) {
        this.scheduler.onAdFinished();

        if (ua.isIOSSmall && !this.notifier.wasFired(VAST_EVENTS.THIRD_QUARTILE)) {
            console.log('[alfa] send skip event');
            this.notifier.markAsFired(VAST_EVENTS.THIRD_QUARTILE);
            this.notifier.markAsFired(VAST_EVENTS.MIDPOINT);
            this.notifier.markAsFired(VAST_EVENTS.FIRST_QUARTILE);

            this.iosfwdlimit = void(0);
            this.notifier.fireLinear(VAST_EVENTS.SKIP);
            this.emit(PLAYER_EVENTS.AD_SKIPPED);
            if (settings.isRotated) {
                this.scheduler.playNextAd();
            } else {
                this.playMainVideo();
            }
            return;
        }

        this.notifyOnce(VAST_EVENTS.COMPLETE);
        this.emit(PLAYER_EVENTS.AD_FINISHED);
        this.ui.hideControlBar(true);
        if (this.config.isAdOnly() && !settings.isRotated) {
            this.scheduler.stage.next(STAGES.BEGIN);
            return;
        }
        if (settings.isRotated) {
            this.scheduler.playNextAd();
            return;
        }
        if (this.scheduler.stage.isPreRoll()) {
            if (VAST_CLOSE_AFTER_SHOW_ONLOAD) {
                this.playMainVideo();
            }
            return;
        } else if (this.scheduler.stage.isMidRoll()) {
            if (this.config.getVideoFile() && VAST_CLOSE_AFTER_SHOW_MIDROLL) {
                this.playMainVideo();
            }
            return;
        }
    }

    if (this.scheduler.stage.isVideo()) {
        this.state.finishedMain = true;
        this.scheduler.stage.next(STAGES.END);
        this.ui.player.pause(); // IE does not pause video on end

        this.ui.showEndingScreen();
        this.scheduler.onMainFinishedEvent();
    }
};

WebAlfaPlayer.prototype.onEnterFullscreen = function () {
    if (this.scheduler.stage.isAd()) {
        this.notifier.fireLinear(VAST_EVENTS.FULLSCREEN);
    }
};

WebAlfaPlayer.prototype.onExitFullscreen = function () {
    if (this.scheduler.stage.isAd()) {
        this.notifier.fireLinear(VAST_EVENTS.EXIT_FULLSCREEN);
    }
};

WebAlfaPlayer.prototype.onMute = function () {
    if (this.scheduler.stage.isAd()) {
        this.notifier.fireLinear(VAST_EVENTS.MUTE);
    }
};

WebAlfaPlayer.prototype.onUnmute = function () {
    if (this.scheduler.stage.isAd()) {
        this.notifier.fireLinear(VAST_EVENTS.UNMUTE);
    }
};


WebAlfaPlayer.prototype.onVideoError = function (passedError) {
    // do nothing if nothing played
    if (this.scheduler.stage.isBegin()) {
        return;
    }

    // if passed error is just an 'Event', then try to get error from VIDEO element
    let error = passedError;
    if (!passedError || (passedError instanceof Event && this.ui.player.error)) {
        error = this.ui.player.error;
    }

    // current media error
    const errorCode = error && error.code || 0;
    const errorMessage = error && error.message || '';
    console.error('[alfa] video error', error);

    // emit error to external listeners
    this.emit(PLAYER_EVENTS.VIDEO_ERROR, error);

    if (this.scheduler.stage.isAd()) {
        console.log('[alfa] cannot play ads file:', this.currentAd && this.currentAd.mediaFile && this.currentAd.mediaFile.url || '');

        if (error instanceof MediaError) {
            // if error is MediaError then convert it to appropriate Linear VastError
            switch (errorCode) {
                case MEDIA_ERROR.MEDIA_ERR_ABORTED:
                    this.notifyVastError(createVastError(VAST_ERRORS.LINEAR_TIMEOUT, errorMessage));
                    break;

                case MEDIA_ERROR.MEDIA_ERR_NETWORK:
                    this.notifyVastError(createVastError(VAST_ERRORS.LINEAR_NOT_FOUND, errorMessage));
                    break;

                case MEDIA_ERROR.MEDIA_ERR_DECODE:
                case MEDIA_ERROR.MEDIA_ERR_SRC_NOT_SUPPORTED:
                    this.notifyVastError(createVastError(VAST_ERRORS.LINEAR_NOT_DISPLAYING, errorMessage));
                    break;

                default:
                    this.notifyVastError(createVastError(VAST_ERRORS.LINEAR_ERROR, errorMessage));
            }
        } else if (error instanceof VastError) {
            // if error is already VastError, then simply send it to server
            this.notifyVastError(error);
        }

        // try to recover from error
        if (settings.isRotated) {
            this.scheduler.playNextAd();
        } else if (this.config.hasPassback()) {
            this.scheduler.tryBackupVast();
        } else {
            this.playMainVideo();
        }
    } else {
        // if main video can't be played, try to pass it
        if (this.mainVideoTime === 0 && this.config.getVideoFile()) {
            window.location = this.config.getVideoFile();
            return;
        }

        this.ui.player.pause();
        this.ui.hideShareButton();
        if (error && error.code === MEDIA_ERROR.MEDIA_ERR_NETWORK) {
            this.ui.showError('Network error');
            this.ui.showRestartButton();
        } else {
            this.ui.showError('Cannot play video file');
        }
        console.log('[alfa] cannot play file: ' + this.config.getVideoFile());
    }
};

WebAlfaPlayer.prototype.onSkipAdClick = function () {
    this.notifier.fireLinear(VAST_EVENTS.SKIP);

    // if current ad is not the last at current pod
    if (this.currentAdNumber < this.getAdTags().length - 1) {
        console.log('[alfa] Play next AD from POD');
        this.currentAdNumber++;
        this.setCurrentAd(this.currentAdNumber);
        this.playCurrentAd();
    } else {
        if (this.config.isVPAID()) {
            this.ui.player.pause();
            this.scheduler.stage.next(STAGES.BEGIN);
        } else {
            this.scheduler.onAdSkipped();

            if (settings.isRotated) {
                this.scheduler.playNextAd();
            } else {
                this.playMainVideo();
                this.ui.hideControlBar(true);
            }
        }
    }
    this.emit(PLAYER_EVENTS.AD_SKIPPED);
};

WebAlfaPlayer.prototype.onCloseClick = function () {
    console.log('[alfa] close click', this);
    this.playMainVideo();
};

WebAlfaPlayer.prototype.onPrerollAdFinished = function () {
    console.log('[alfa] preroll finished');
};

export default function (pui) {
    const wap = new WebAlfaPlayer(pui);
    return {
        create: wap.create.bind(wap),
        getShareLink: wap.getShareLink.bind(wap),
        showAd: wap.showAd.bind(wap),
        addEventListener: wap.addEventListener.bind(wap),
        removeEventListener: wap.removeEventListener.bind(wap),
        play: wap.play.bind(wap),
        pause: wap.pause.bind(wap),

        get currentTime() {
            return wap.currentTime;
        },
        set currentTime(value) {
            wap.currentTime = value;
        },

        get volume() {
            return wap.ui.getVolume();
        },
        set volume(value) {
            wap.ui.setVolume(value);
        },

        get vpaid() {
            return wap.ui.vpaidPlayer;
        },

        get version() {
            return process.env.VERSION || 'dev';
        },
        get commit() {
            return process.env.ALFA_COMMIT || '';
        }
    };
}

export const PLAYER_VERSION = process.env.VERSION || 'dev';
export const PLAYER_COMMIT = process.env.ALFA_COMMIT || '';



