import { registerGlobalErrorHandler } from '@/system/errors';
import WebAlfaPlayer, { PLAYER_VERSION, PLAYER_COMMIT } from '@/WebAlfaPlayer';
import { settings, INITIAL_SID } from '@/data/settings';
import { notifyInfo } from '@/system/notifier';

// registration of global error handler
// registerGlobalErrorHandler(window);

// if ROTATED build - enable ad rotation
if (process.env.ALFA_ROTATED) {
    settings.isRotated = true;
}

// notify server about LOAD event
notifyInfo('load', { sid: INITIAL_SID });

// if VPAID build - expose 'getVPAIDAd' function to global
if (process.env.ALFA_VPAID) {
    window.getVPAIDAd = require('@/VPAIDVideoPlayer').getVPAIDAd;
}

// expose player to global variable
window.WebAlfaPlayer = WebAlfaPlayer;

// if we in DEV mode, write console version and commit info
if (settings.isDev) {
    console.log(`[alfa] version '${ PLAYER_VERSION }', commit '${ PLAYER_COMMIT }'`);
}



