import { parsedUA as ua } from '@/data/userAgent';
import EventSystem from '@/system/eventSystem';
import { getShortURL } from '@/system/networking';
import socialNetworks from '@/data/socialNetworks';
import { isChildOf } from './utils';
import { STYLES } from '@/ui/styles';

class SocialButton extends EventSystem {
    constructor(social, isBig) {
        super();

        this.element = null;
        this.isBig = isBig;
        this.social = social;

        this.hideDialogTimer = null;
    }

    create(parent) {
        this.element = document.createElement('a');
        this.element.className = 'alfa-social-button alfa-social-' + this.social.name;
        if (this.isBig) {
            this.element.classList.add('alfa-social-button-big');
        }
        this.element.setAttribute('target', '_blank');

        this.element.addEventListener('click', (e) => this.emit('click', e));

        parent.appendChild(this.element);
    }

    setLink(link) {
        this.element.setAttribute('href', link);
    }
}

export const EVENTS = {
    DONE: 'done'
};

export class ShareDialog extends EventSystem {
    constructor(isEndScreen) {
        super();

        this.element = null;
        this.isEndScreen = isEndScreen;
        this.shareButtons = [];
    }

    create(parent) {
        this.element = document.createElement('div');
        if (!this.isEndScreen) {
            this.element.className = `alfa-share-background ${STYLES.TRANSITION_VISIBLE} ${STYLES.OPACITY_HIDDEN}`;
        } else {
            this.element.className = 'alfa-share-background-big';
        }

        const socialButtonsElement = document.createElement('div');
        socialButtonsElement.className = 'alfa-share-socials';

        this.element.appendChild(socialButtonsElement);

        parent.appendChild(this.element);

        this.shareButtons = socialNetworks.map((network) => {
            const socialButton = new SocialButton(network, this.isEndScreen);
            socialButton.create(socialButtonsElement);
            socialButton.on('click', () => {
                // if dialog is in popup mode, then close it after button click
                if (!this.isEndScreen) {
                    this.hide();
                }
                this.emit(EVENTS.DONE);
            });
            return socialButton;
        });

        // if dialog is not on endscreen, then setup auto hide behaviour
        if (!this.isEndScreen) {
            this.element.addEventListener('mouseout', (ev) => {
                let root = this.element;
                if (!isChildOf(ev.relatedTarget, root) && (ev.relatedTarget !== root)) {
                    this.setupHideTimeout(1000);
                }
            });
            this.element.addEventListener('mousemove', () => {
                this.resetHideTimeout();
            }, true);
        }
    }

    show(shareURL) {
        getShortURL(shareURL, (responseText) => {
            if (!responseText) {
                console.log('[alfa] Empty share short response');
                return;
            }

            this.shareButtons.forEach((button) => {
                let link = button.social.shareUrl(responseText);
                button.setLink(link);
            });
        });

        if (!this.isEndScreen) {
            this.resetHideTimeout();

            // actually showing element
            this.element.classList.remove(STYLES.OPACITY_HIDDEN);

            if (ua.isMobile) {
                this.setupHideTimeout(3000);
            }
        }
    }

    hide() {
        if (!this.element) {
            return;
        }

        this.element.classList.add(STYLES.OPACITY_HIDDEN);
    }

    isVisible() {
        if (this.element) {
            return !this.element.classList.contains(STYLES.OPACITY_HIDDEN);
        }

        return false;
    }

    resetHideTimeout() {
        clearTimeout(this.hideDialogTimer);

        this.element.classList.remove(STYLES.OPACITY_HIDDEN);
    }

    setupHideTimeout(timeout) {
        clearTimeout(this.hideDialogTimer);

        // hide dialog after timeout
        this.hideDialogTimer = setTimeout(() => {
            this.hide();
            this.emit(EVENTS.DONE);
        }, timeout || 3000);
    }
}



