import { Icon, EVENTS as ICON_EVENTS } from './icon';
import EventSystem from '@/system/eventSystem';

export const EVENTS = {
    ICON_VIEW: 'icon_view',
    ICON_CLICK: 'icon_click',
};

/**
 * Container element for VAST icons
 */
export class IconsContainer extends EventSystem {
    constructor() {
        super();

        this.element = null;
        this.icons = [];
    }

    create(parent) {
        if (this.element) {
            this.remove();
        }

        this.element = document.createElement('div');
        this.element.className = 'alfa-icons-container';

        parent.appendChild(this.element);
    }

    remove() {
        const element = this.element;
        if (element && element.parentNode) {
            element.parentNode.removeChild(element);
        }
    }

    resizeToVideo(videoElement) {
        if (!this.element) {
            return;
        }

        const videoElementRect = videoElement.getBoundingClientRect();

        let top, height, left, width;
        if (videoElement && videoElement.videoHeight > 0) {
            if (videoElement.videoHeight / videoElementRect.height <= videoElement.videoWidth / videoElementRect.width) {
                const relativeHeight = videoElement.videoHeight / (videoElement.videoWidth / videoElementRect.width);
                top = ((videoElementRect.height - relativeHeight) / 2) + 'px';
                height = relativeHeight + 'px';
                left = '0';
                width = '100%';
            } else {
                const relativeWidth = videoElement.videoWidth / (videoElement.videoHeight / videoElementRect.height);
                left = ((videoElementRect.width - relativeWidth) / 2) + 'px';
                width = relativeWidth + 'px';
                top = '0';
                height = '100%';
            }
        } else {
            top = '0';
            height = '100%';
            left = '0';
            width = '100%';
        }

        // set new dimensions
        const element = this.element;
        element.style.top = top;
        element.style.height = height;
        element.style.left = left;
        element.style.width = width;
    }

    showIcons(newIconRecords) {
        if (!this.element) {
            return;
        }

        const visibleIconElements = this.icons;

        // filter visible icons that should be destroyed
        const iconsToDestroy = visibleIconElements.filter((iconElement) => {
            const newIconRecordsByIndex = newIconRecords.filter((newIconRecord) => newIconRecord.index === iconElement.data.index);
            return newIconRecordsByIndex.length <= 0;
        });
        // filter new icons data - for Icon element creation
        const iconRecordsToCreate = newIconRecords.filter((newIconRecord) => {
            const visibleIconElementsByIndex = visibleIconElements.filter((iconElement) => iconElement.data.index === newIconRecord.index);
            return visibleIconElementsByIndex.length <= 0;
        });

        // destroy invalid icons
        iconsToDestroy.forEach((iconElement) => {
            const index = visibleIconElements.indexOf(iconElement);
            if (index >= 0) {
                visibleIconElements.slice(index, 1);
            }

            iconElement.off(ICON_EVENTS.VIEW);
            iconElement.off(ICON_EVENTS.CLICK);

            iconElement.remove();
        });

        // create new icons
        iconRecordsToCreate.forEach((iconRecord) => {
            const iconElement = new Icon(iconRecord);
            iconElement.create(this.element);

            iconElement.on(ICON_EVENTS.VIEW, (icon) => {
                this.emit(EVENTS.ICON_VIEW, icon);
            });
            iconElement.on(ICON_EVENTS.CLICK, (icon) => {
                this.emit(EVENTS.ICON_CLICK, icon);
            });

            visibleIconElements.push(iconElement);
        });
    }
}



