import EventSystem from '@/system/eventSystem';

const VIEWABILITY_THRESHOLD = 0.5;  // 50% is threshold to generate viewability change event
const VIEWABILITY_CHECK_TIMEOUT = 500;  // ms, timeout to check viewability, if IntersectionObserver is not supported
const ACTIVE_TAB_CHECK_TIMEOUT = 500;  // ms, timeout to check window focus and active tab status

export const EVENTS = {
    VISIBLE: 'visible',
    HIDDEN: 'hidden',

    VIEWABILITY: 'viewability',
    FOCUS: 'focus',
    ACTIVITY: 'activity'
};

export class ViewabilityObserver extends EventSystem {
    constructor(element) {
        super();

        this.isFocused = true;
        this.isActiveTab = isActiveTab();
        this.visibilityRatio = getViewability(element);

        // init FOCUS checks
        this.__cancelFocusChecks = initFocusChecks((focused) => {
            if (this.isFocused !== focused) {
                this.isFocused = focused;
                this.emit(EVENTS.FOCUS, focused);
                checkVisibility();
            }
        });

        // init ACTIVE_TAB checks
        this.__cancelActiveTabChecks = initActiveTabChecks((active) => {
            if (this.isActiveTab !== active) {
                this.isActiveTab = active;
                this.emit(EVENTS.ACTIVITY, active);
                checkVisibility();
            }
        });

        // init VIEWABILITY checks
        let previousVisible = null;
        this.__cancelViewabilityChecks = initViewabilityChecks(element, (ratio) => {
            this.visibilityRatio = ratio;
            const isVisible = ratio >= VIEWABILITY_THRESHOLD;

            // emit event, only if threshold is crossed
            if (previousVisible !== isVisible) {
                previousVisible = isVisible;

                // emit event
                const viewability = { ratio, isVisible };
                this.emit(EVENTS.VIEWABILITY, viewability);
                checkVisibility();
            }
        });

        const checkVisibility = () => {
            if (this.visibilityRatio >= VIEWABILITY_THRESHOLD && this.isFocused && this.isActiveTab) {
                this.emit(EVENTS.VISIBLE);
            } else {
                this.emit(EVENTS.HIDDEN);
            }
        };
    }

    cancel() {
        this.__cancelFocusChecks();
        this.__cancelActiveTabChecks();
        this.__cancelViewabilityChecks();
    }

    getViewability() {
        return {
            visibility: Math.ceil(this.visibilityRatio * 100),
            focused: this.isFocused,
            active: this.isActiveTab
        };
    }
}

function initViewabilityChecks(vo, callback) {
    if (isIntersectionObserverSupported()) {
        return initViewabilityChecksWithObserver(vo, callback);
    } else {
        return initViewabilityChecksWithInterval(vo, callback);
    }
}

function initViewabilityChecksWithObserver(element, callback) {
    const options = {
        threshold: [VIEWABILITY_THRESHOLD]
    };
    const io = new IntersectionObserver((entries) => {
        if (entries && entries.length > 0) {
            callback(entries[0].intersectionRatio);
        }
    }, options);
    io.observe(element);

    // cancelling function
    return () => io.disconnect();
}

function initViewabilityChecksWithInterval(element, callback) {
    function viewabilityCallback() {
        const ratio = getViewability(element);
        callback(ratio);
    }

    // setup repeatable viewability checks
    const interval = setInterval(viewabilityCallback, VIEWABILITY_CHECK_TIMEOUT);
    // run first check on next tick
    setTimeout(viewabilityCallback, 0);

    // cancelling function
    return () => clearInterval(interval);
}

function initFocusChecks(callback) {
    const onBlur = () => callback(false);
    window.addEventListener('blur', onBlur);

    const onFocus = () => callback(true);
    window.addEventListener('focus', onFocus);

    // cancelling function
    return () => {
        window.removeEventListener('blur', onBlur);
        window.removeEventListener('focus', onFocus);
    };
}

function initActiveTabChecks(callback) {
    function checkCallback() {
        callback(isActiveTab());
    }

    // setup repeatable viewability checks
    const interval = setInterval(checkCallback, ACTIVE_TAB_CHECK_TIMEOUT);
    // run first check on next tick
    setTimeout(checkCallback, 0);

    // cancelling function
    return () => clearInterval(interval);
}

export const isActiveTab = (function() {
    if ('hidden' in document) {
        return function () {
            return !document.hidden;
        };
    }
    if ('webkitHidden' in document) {
        return function () {
            return !document.webkitHidden;
        };
    }
    // document.hidden not supported
    return function () {
        return true;
    };
})();

export function getViewability(element) {
    const viewport = getViewportProps();
    const positions = getElementProps(element, viewport);

    return calculateViewability(positions, viewport);
}

function getViewportProps() {
    return {
        h: window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight || 0,
        w: window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth || 0,
        sl: window.pageXOffset !== undefined ? window.pageXOffset : (document.documentElement || document.body.parentNode || document.body).scrollLeft,
        st: window.pageYOffset !== undefined ? window.pageYOffset : (document.documentElement || document.body.parentNode || document.body).scrollTop
    };
}

function getElementProps(element, viewport) {
    // Get the bounding rectangle of our element
    const elementBoundingBox = element.getBoundingClientRect();
    return {
        x: elementBoundingBox.left + viewport.sl,
        y: elementBoundingBox.top + viewport.st,
        w: elementBoundingBox.width,
        h: elementBoundingBox.height
    };
}

function calculateViewability(pos, stage) {
    let horizontal = 0;
    if (pos.x < stage.sl) {
        horizontal += stage.sl - pos.x;
    }
    if (pos.x + pos.w > stage.sl + stage.w) {
        horizontal += pos.x + pos.w - (stage.sl + stage.w);
    }
    if (horizontal > pos.w) {
        horizontal = pos.w;
    }

    let vertical = 0;
    if (pos.y < stage.st) {
        vertical += stage.st - pos.y;
    }
    if (pos.y + pos.h > stage.st + stage.h) {
        vertical += pos.y + pos.h - (stage.st + stage.h);
    }
    if (vertical > pos.h) {
        vertical = pos.h;
    }

    return (pos.w - horizontal) * (pos.h - vertical) / (pos.w * pos.h);
}

function isIntersectionObserverSupported() {
    return 'IntersectionObserver' in window &&
        'IntersectionObserverEntry' in window &&
        'intersectionRatio' in window.IntersectionObserverEntry.prototype;
}



