import EventSystem from '@/system/eventSystem';
import { STYLES } from '@/ui/styles';

export const EVENTS = {
    CLICK: 'click'
};

export class ButtonMute extends EventSystem {
    constructor() {
        super();

        this.element = null;
    }

    create(parent) {
        const element = this.element = document.createElement('div');
        element.className = STYLES.MUTE_BUTTON;
        element.addEventListener('click', () => {
            this.emit(EVENTS.CLICK);
        });

        parent.appendChild(element);
    }

    remove() {
        if (this.element && this.element.parentNode) {
            this.element.parentNode.removeChild(this.element);
        }
    }

    muted(value) {
        if (!this.element) {
            return;
        }

        if (value) {
            this.element.classList.add(STYLES.MUTE_BUTTON__MUTED);
        } else {
            this.element.classList.remove(STYLES.MUTE_BUTTON__MUTED);
        }
    }
}



