import { Vast, AdTag, Linear, MediaFile } from './data';

export default class ParserVast1 {
    constructor(version) {
        this.version = version;
        this.wrapperCounter = 0;
    }

    parse(xml, source, callback) {
        const adTag = new AdTag();

        const ad = xml.getElementsByTagName('Ad')[0];
        try {
            const video = ad.getElementsByTagName('Video')[0];

            const mediaFile = new MediaFile();
            let mediaFileTag = video.getElementsByTagName('MediaFile');
            mediaFile.url = mediaFileTag[0].getElementsByTagName('URL')[0].firstChild.data;

            const linear = new Linear();
            linear.mediaFiles.push(mediaFile);
            linear.clickThrough = video.getElementsByTagName('VideoClicks')[0].getElementsByTagName('ClickThrough')[0].getElementsByTagName('URL')[0].firstChild.data;

            adTag.linears.push(linear);
        } catch (e) {
            console.error('[alfa] Error parsing vast version 1 (video section)', e);
        }

        const urls = ad.getElementsByTagName('Impression')[0].getElementsByTagName('URL');
        for (let i = 0; i < urls.length; i++) {
            adTag.impressions.push(urls[i].firstChild.data);
        }

        const tracking = ad.getElementsByTagName('TrackingEvents')[0].getElementsByTagName('Tracking');
        for (let i = 0; i < tracking.length; i++) {
            let eventName = tracking[i].getAttribute('event');
            if (!eventName)
                continue;

            const urls = tracking[i].getElementsByTagName('URL');
            adTag.eventsLinear[eventName.toLowerCase()] = [];
            for (let j = 0; j < urls.length; j++) {
                if (urls[j].firstChild.data)
                    adTag.eventsLinear[eventName.toLowerCase()].push(urls[j].firstChild.data);
            }
        }

        const vast = new Vast(this.version);
        vast.adTags.push(adTag);
        vast.source = source;
        callback(null, vast);
    }
}



