import EventSystem from '@/system/eventSystem';
import enableInlineVideo from 'iphone-inline-video';
import { MEDIA_EVENTS } from '@/data/constants';
import { proxyEvents } from '@/ui/utils';

export class VideoPlayer extends EventSystem {
    constructor() {
        super();

        this.element = null;

        __wrapFunctionCall(this, 'load');
        __wrapFunctionCall(this, 'play');
        __wrapFunctionCall(this, 'pause');
        __wrapProperty(this, 'paused', { getter: true });
        __wrapProperty(this, 'readyState', { getter: true });
        __wrapProperty(this, 'volume', { getter: true, setter: true });
        __wrapProperty(this, 'error', { getter: true });
        __wrapProperty(this, 'duration', { getter: true });
        __wrapProperty(this, 'currentTime', { getter: true, setter: true });
        __wrapProperty(this, 'buffered', { getter: true });
        __wrapProperty(this, 'src', { getter: true, setter: true });
    }

    createElement(parent, config) {
        const element = this.element = document.createElement('video');
        element.style.cssText = 'width:100%;height:100%';

        // HINT: added for iOS - elements will now be allowed to play inline, and will not automatically enter fullscreen mode when playback begins
        element.setAttribute('webkit-playsinline', '');
        element.setAttribute('playsinline', '');

        // HINT: support autoplay on iOS <= 9 (iphone and ipad)
        enableInlineVideo(element, { iPad: true });

        // mute video to support autoplay without user gesture on:
        //   - iOS
        //   - Android
        //   - Chrome 66+
        if (config.isMute()) {
            this.mute(true);
        }

        this.__initElement(element);

        parent.appendChild(element);
    }

    useElement(element) {
        this.element = element;

        this.__initElement(element);
    }

    addSources(sources) {
        sources.forEach((source) => {
            const sourceElement = document.createElement('source');
            sourceElement.src = source.src;
            sourceElement.type = source.type;
            this.element.appendChild(sourceElement);
        });
    }

    show() {
        if (!this.element) {
            return;
        }

        this.element.style.visibility = 'visible';
        this.element.style.display = 'inline';
    }

    hide() {
        if (!this.element) {
            return;
        }

        this.element.style.visibility = 'hidden';
        this.element.style.display = 'none';
    }

    mute(value) {
        if (!this.element) {
            return;
        }

        if (value) {
            this.element.setAttribute('muted', '');
        } else {
            this.element.removeAttribute('muted');
        }
        this.element.muted = value;
    }

    hasDuration() {
        return !isNaN(this.element.duration);
    }

    isEnded() {
        return this.hasDuration() && this.element.currentTime >= this.element.duration;
    }

    __initElement(element) {
        // pass all media events from DOM element to current player
        proxyEvents(Object.keys(MEDIA_EVENTS), element, this);
    }
}

function __wrapFunctionCall(player, functionName) {
    player[functionName] = (...params) => {
        if (!player.element) {
            console.log(`Trying to call method '${functionName}' of player while DOM element is not created yet`);
        }

        return player.element[functionName].apply(player.element, params);
    };
}

function __wrapProperty(player, propertyName, { getter, setter }) {
    const property = {};
    if (getter) {
        property['get'] = function () {
            if (!player.element) {
                console.log(`Trying to use getter '${propertyName}' of player while DOM element is not created yet`);
            }

            return player.element[propertyName];
        };
    }
    if (setter) {
        property['set'] = function (value) {
            if (!player.element) {
                console.log(`Trying to use setter '${propertyName}' of player while DOM element is not created yet`);
            }

            player.element[propertyName] = value;
        };
    }

    Object.defineProperty(player, propertyName, property);
}



