import { parsedUA as ua } from '@/data/userAgent';
import EventSystem from '@/system/eventSystem';
import { TimeBar, EVENTS as TIME_BAR_EVENTS } from './timeBar';
import { TimeLabel } from './timeLabel';
import { VolumeBar, EVENTS as VOLUME_BAR_EVENTS } from './volumeBar';
import { isChildOf } from './utils';
import { STYLES } from '@/ui/styles';

export const EVENTS = {
    TIME_SEEK_START: 'time_seek_start',
    TIME_SEEK: 'time_seek',
    TIME_SEEK_END: 'time_seek_end',

    PLAY_PAUSE_CLICK: 'play_pause_click',
    VOLUME_CLICK: 'volume_click',
    VOLUME_CHANGE: 'volume_change',
    FULLSCREEN_CLICK: 'fullscreen_click'
};

export class ControlBar extends EventSystem {
    constructor() {
        super();

        this.container = null;
        this.element = null;
        this.playButton = null;

        this.timeBar = new TimeBar();
        this.currentTime = new TimeLabel('alfa-current-time');
        this.totalTime = new TimeLabel('alfa-total-time');

        this.volumeButton = null;
        this.volumeBar = null;
    }

    create(parent, config) {
        const container = this.container = document.createElement('div');
        container.className = `${STYLES.CONTROLS_CONTAINER}`;

        const background = document.createElement('div');
        background.className = `${STYLES.CONTROLS_BACKGROUND}`;
        container.appendChild(background);

        const controls = this.element = document.createElement('span');
        controls.className = `${STYLES.CONTROLS} ${STYLES.TRANSITION_VISIBLE}`;
        container.appendChild(controls);

        this.playButton = document.createElement('span');
        this.playButton.className = `alfa-play-button play-icon ${STYLES.CONTROLS_BUTTON} ${STYLES.NO_SELECT}`;
        controls.appendChild(this.playButton);

        this.timeBar.create(controls);
        this.currentTime.create(controls);
        this.totalTime.create(controls);

        if (!ua.isMobile) {
            this.createVolumeRegulator(controls);
        }

        if (!config.isFullscreenRestricted()) {
            this.createFullscreenButton(controls);
        }

        this.playButton.addEventListener(ua.isMobile ? 'touchend' : 'click', (e) => {
            e.stopPropagation();
            this.emit(EVENTS.PLAY_PAUSE_CLICK);
        });

        this.timeBar.on(TIME_BAR_EVENTS.SEEK_START, () => this.emit(EVENTS.TIME_SEEK_START));
        this.timeBar.on(TIME_BAR_EVENTS.SEEK, (percent) => this.emit(EVENTS.TIME_SEEK, percent));
        this.timeBar.on(TIME_BAR_EVENTS.SEEK_END, () => this.emit(EVENTS.TIME_SEEK_END));

        parent.appendChild(container);
    }

    createVolumeRegulator(parent) {
        const container = document.createElement('div');
        container.className = `alfa-volume__container ${STYLES.CONTROLS_BUTTON}`;

        const bar = this.volumeBar = new VolumeBar();
        bar.create(container);
        bar.on(VOLUME_BAR_EVENTS.VOLUME_CHANGE, (percent) => this.emit(EVENTS.VOLUME_CHANGE, percent));

        const button = this.volumeButton = document.createElement('div');
        button.className = `${STYLES.BUTTON_VOLUME}`;
        button.addEventListener('click', (e) => {
            e.stopPropagation();
            this.emit(EVENTS.VOLUME_CLICK);
        });
        container.addEventListener('mouseenter', () => bar.show());
        container.addEventListener('mouseout', (ev) => {
            const related = ev.relatedTarget;
            if (!isChildOf(related, container)) {
                bar.hide();
            }
        });
        container.appendChild(button);

        parent.appendChild(container);
    }

    createFullscreenButton(parent) {
        const fullScreenButton = document.createElement('span');
        fullScreenButton.className = `alfa-fullscreen-button ${STYLES.CONTROLS_BUTTON}`;
        fullScreenButton.addEventListener('click', (e) => {
            e.stopPropagation();
            this.emit(EVENTS.FULLSCREEN_CLICK);
        });

        parent.appendChild(fullScreenButton);
    }

    show() {
        if (!this.container) {
            return;
        }

        this.container.classList.remove(STYLES.DISPLAY_NONE);
        this.container.classList.remove(STYLES.OPACITY_HIDDEN);
    }

    hide(immediate) {
        if (immediate) {
            this.container.classList.add(STYLES.DISPLAY_NONE);
        }
        this.container.classList.add(STYLES.OPACITY_HIDDEN);
    }

    isVisible() {
        return !this.container.classList.contains(STYLES.DISPLAY_NONE)
            && !this.container.classList.contains(STYLES.OPACITY_HIDDEN);
    }

    morphToAdsLayout() {
        this.currentTime.setText('Wait for ad...');
        this.timeBar.hide();
        this.totalTime.hide();

        this.element.classList.add(STYLES.CONTROLS_ADS_LAYOUT);
    }

    morphToWaitingAdsLayout() {
        this.currentTime.setText('Wait for ad...');
    }

    morphToNormalLayout() {
        if (!this.isAdsLayout()) {
            return;
        }

        this.timeBar.show();
        this.totalTime.show();
        this.timeBar.setTime(0);
        this.element.classList.remove(STYLES.CONTROLS_ADS_LAYOUT);
    }

    isAdsLayout() {
        return this.element.classList.contains(STYLES.CONTROLS_ADS_LAYOUT);
    }

    showPlayButton() {
        this.playButton.classList.remove(STYLES.ICON_PAUSE);
        this.playButton.classList.add(STYLES.ICON_PLAY);
    }

    showPauseButton() {
        this.playButton.classList.remove(STYLES.ICON_PLAY);
        this.playButton.classList.add(STYLES.ICON_PAUSE);
    }

    updateVolume(vol) {
        if (!this.volumeButton) {
            return;
        }

        this.volumeBar.setVolume(vol);

        if (vol === 0) {
            this.volumeButton.classList.add(STYLES.BUTTON_VOLUME_MUTE);
        } else {
            this.volumeButton.classList.remove(STYLES.BUTTON_VOLUME_MUTE);
        }
    }
}



