import EventSystem from '@/system/eventSystem';
import { settings } from '@/data/settings';

export const EVENTS = {
    CLICK: 'click'
};

export class SkipButton extends EventSystem {
    constructor(skipOffset) {
        super();

        this.element = null;
        this.skipText = null;
        this.skipTime = 999;
        this.skipOffset = skipOffset || settings.skipAdOffset;
    }

    create(parent) {
        const element = this.element = document.createElement('div');
        element.className = 'alfa-skip-button';
        element.addEventListener('click', () => {
            // emit CLICK only after skip interval is passed
            if (this.skipTime <= 0) {
                this.emit(EVENTS.CLICK);
            }
        });

        const skipText = this.skipText = document.createElement('span');
        skipText.className = 'alfa-skip-button__text';
        element.appendChild(skipText);

        parent.appendChild(element);
    }

    remove() {
        if (this.element && this.element.parentNode) {
            this.element.parentNode.removeChild(this.element);
        }
    }

    update(currentTime) {
        this.skipTime = Math.round(this.skipOffset - currentTime);
        if (this.skipTime > 0) {
            this.skipText.innerHTML = 'Skip Ad in ' + this.skipTime;
        } else {
            this.skipText.innerHTML = 'SKIP AD';
        }
    }
}



