import EventSystem from '@/system/eventSystem';
import { STYLES } from '@/ui/styles';

export const EVENTS = {
    VIEW: 'view',
    CLICK: 'click',
};

/**
 * VAST icon element
 */
export class Icon extends EventSystem {
    constructor(data) {
        super();

        this.element = null;
        this.data = data;
    }

    create(parent) {
        const element = this.element = document.createElement('div');
        element.className = 'alfa-icon';
        if (this.data.clickThrough) {
            element.classList.add(STYLES.CLICKABLE);
        }
        element.style.width = this.data.width + 'px';
        element.style.height = this.data.height + 'px';

        positionElement(element, this.data);

        const img = document.createElement('img');
        img.src = this.data.staticResource;

        // emit event after successful image loading
        img.onload = () => {
            this.emit(EVENTS.VIEW, this.data);
        };

        // click listener
        img.addEventListener('click', () => {
            this.emit(EVENTS.CLICK, this.data);
        });

        element.appendChild(img);

        parent.appendChild(element);
    }

    remove() {
        if (this.element && this.element.parentNode) {
            this.element.parentNode.removeChild(this.element);
        }
    }
}

function positionElement(element, data) {
    const x = String(data.xPosition).toLowerCase();
    if (x === 'left') {
        element.style.left = '0';
    } else if (x === 'right') {
        element.style.right = '0';
    } else {
        element.style.left = x + 'px';
    }

    const y = String(data.yPosition).toLowerCase();
    if (y === 'top') {
        element.style.top = '0';
    } else if (y === 'bottom') {
        element.style.bottom = '0';
    } else {
        element.style.top = y + 'px';
    }
}



