import EventSystem from '@/system/eventSystem';
import { STYLES } from '@/ui/styles';

export const EVENTS = {
    AGREED: 'agreed',
    REJECT: 'reject'
};

const WARNING_TEXT = '' +
    'By entering this video, clicking the green "Show Me" button below, you agree to the following terms and conditions: ' +
    'You certify that you are 18 years of age or older, and are not offended by sexually explicit imagery. ' +
    'You agree that you will not permit any person(s) under 18 years of age to have access to the video.';

export class AdultDialog extends EventSystem {
    constructor() {
        super();

        this.element = null;
    }

    create(parent) {
        this.element = document.createElement('div');
        this.element.className = 'alfa-adult-warning-base';

        const innerBase = document.createElement('div');
        innerBase.className = 'alfa-adult-inner-base';

        const innerCenter = document.createElement('div');
        innerCenter.className = 'alfa-adult-inner-center';

        const mainText = document.createElement('div');
        mainText.className = `alfa-adult-main-text ${STYLES.NO_SELECT}`;
        mainText.appendChild(document.createTextNode('Adult Only!'));

        const warningText = document.createElement('div');
        warningText.className = `alfa-adult-warning-text ${STYLES.NO_SELECT}`;
        warningText.appendChild(document.createTextNode(WARNING_TEXT));

        const showButton = document.createElement('div');
        showButton.appendChild(document.createTextNode('Show me'));
        showButton.className = `alfa-adult-show-button ${STYLES.NO_SELECT}`;
        showButton.addEventListener('click', () => {
            this.remove();
            this.emit(EVENTS.AGREED);
        });

        const showButtonRow = document.createElement('div');
        showButtonRow.appendChild(showButton);
        showButtonRow.className = 'alfa-adult-showme';

        const leaveButton = document.createElement('div');
        leaveButton.className = `alfa-adult-leave ${STYLES.NO_SELECT}`;
        leaveButton.appendChild(document.createTextNode('Leave'));
        leaveButton.addEventListener('click', () => {
            this.emit(EVENTS.REJECT);
        });

        innerCenter.appendChild(mainText);
        innerCenter.appendChild(warningText);
        innerCenter.appendChild(showButtonRow);
        innerBase.appendChild(innerCenter);
        innerBase.appendChild(leaveButton);
        this.element.appendChild(innerBase);

        parent.appendChild(this.element);
    }

    remove() {
        if (this.element && this.element.parentNode) {
            this.element.parentNode.removeChild(this.element);
        }
    }
}



